import TimelineRoundedIcon from '@material-ui/icons/TimelineRounded';
import ImportantDevicesRoundedIcon from '@material-ui/icons/ImportantDevicesRounded';
import StoreMallDirectoryRoundedIcon from '@material-ui/icons/StoreMallDirectoryRounded';
import LockOpenRoundedIcon from '@material-ui/icons/LockOpenRounded';
import SettingsRoundedIcon from '@material-ui/icons/SettingsRounded';
import SupervisorAccountRoundedIcon from '@material-ui/icons/SupervisorAccountRounded';
import history from '../../handlers/history'

export default class Dashboard{
    constructor(){}
    static sidebar(business, hookImage){
        let businessActive = business.filter(company => company.active === true)[0]
        let branchesAvailable = []
        const imageStyles = {
            width: "30px",
            height: "30px",
            borderRadius: "50%"
        }
        if( business.length >= 1 ){
            for( let item of business ){
                const image = hookImage(item.logo)
                branchesAvailable.push({
                    icon: Boolean(image) ? <img style={imageStyles} src={image} alt="business-image" /> : <StoreMallDirectoryRoundedIcon />,
                    title: item.businessName,
                    path: undefined,
                    defaultActive: item.active,
                    helpperText: item.active ? "Usando ahora" : "",
                    onClick: () => null,
                })
            }
        }
        const sufixPath = businessActive.businessName.replace(/ /g, "-")
        return [
            {
                title: "Mi Negocio",
                children: [
                    {
                        icon: <TimelineRoundedIcon />,
                        title: "Análisis",
                        path: `/`,
                        onClick: () => history.push(`/`),
                    },{
                        icon: <ImportantDevicesRoundedIcon />,
                        title: "Administrar",
                        onClick: () => history.push(`/${sufixPath}`),
                        path: `/${sufixPath}`,
                    },{
                        icon: <LockOpenRoundedIcon />,
                        title: "Roles y permisos",
                        onClick: () => history.push(`/app/permissions`),
                        path: `/app/permissions`,
                    },{
                        icon: <SettingsRoundedIcon />,
                        title: "Configurar negocio",
                        onClick: () => history.push(`/${sufixPath}/setting`),
                        path: `/${sufixPath}/setting`,
                    },{
                        icon: <SupervisorAccountRoundedIcon />,
                        title: "Usuarios",
                        onClick: () => null,
                        path: ``,
                    }
                ]
            },{
                title: "Mis Sucursales",
                children: branchesAvailable
            }
        ]
    }
}