import React from 'react'
import Sidebar from './Sidebar'
import Navbar from './Navbar'
import Sockets from '../../../socket/Socket'

const Container = ({children, business}) => {
	const [openDrawe, setOpenDrawer, ] = React.useState(true)
	return(
		<>
			<Navbar openDrawe={openDrawe} setOpenDrawer={setOpenDrawer} />
			<Sidebar business={business} setOpenDrawer={setOpenDrawer} openDrawe={openDrawe} >
				{ children }
			</Sidebar>
		</>
	)
}

export default Container