import Permissions from '../../views/roleandpermissions/class'
import constant from './index'

export const loadListOfPermissions = (permissions) => {
	return {type: constant.LOAD_LIST_OF_PERMISSIONS, permissions}
}
export const openFormCreateRol = (value) => {
	return {type: constant.OPEN_FORM_CREATE_ROL, value}
}
export const updateFormType = (typeForm) => {
	return {type: constant.UPDATE_FORM_TYPE, typeForm}
}
export const updateFormCreateRol = (form) => {
	return {type: constant.UPDATE_FORM_CREATE_ROL, form}
}
export const selectRol = (role) => {
	return {type: constant.SELECT_ROL, role}
}
export const getPermissions = () => async (dispatch) => {
	try{
		const permit = new Permissions({})
		const permissions = await permit.get()
		if( permissions.status ){
			dispatch(loadListOfPermissions(permissions.payload))
		}
	}catch(err){
		console.log(err)
	}
}
export const loadListOfRoles = (roles) => {
	return {type: constant.LOAD_LIST_OF_ROLES, roles}
}
export const getRoles = () => async (dispatch) => {
	try{
		const permit = new Permissions({})
		const roles = await permit.getRoles()
		dispatch(loadListOfRoles(roles.payload))
	}catch(err){
		console.log(err)
	}
}