import app from '../../connection/Api'

const methodsDefault = {
    alert : () => null,
    callRoles : () => null,
    selectRol : () => null,
    openFormCreateRol : () => null,
}
export default class Permissions{
    constructor({role={}, methods=methodsDefault, roleForm, form}){
        this.form = form
        this.roleForm = roleForm
        this.role = role
        this.methods = methods
    }
    async updateRole(payload){
        try{
            const role = await app.post(`/update/role`, {role: payload})
            if( role.status ){
                const message = <p>El rol <b>{role.payload.name}</b> fue actualizado con exito</p>
                this.methods.alert(true,message, "success")
                return role
            }
        }catch(err){
            console.log(err)
        }
    }
    async deleteRole(role){
        try{
            const roleDelete = await app.get(`/delete/role?r=${role._id}`)
            if( roleDelete.status ){
                this.methods.alert(true,"El rol fue eliminado con exito", "success")
                return roleDelete
            }
        }catch(err){
            console.log(err)
        }
    }
    async createRole(payload){
        try{
            const role = await app.post(`/create/role`, {role: payload})
            if( role.status ){
                const message = <p>El rol <b>{role.payload.name}</b> fue creado con exito</p>
                this.methods.alert(true,message, "success")
                return role
            }
        }catch(err){
            console.log(err)
        }
    }
    async updatePermissionsInRole(permissions){
        try{
            let ids = []
            const role = {...this.role}
            for( let permit  of permissions ){
                ids.push(permit._id)
            }
            role.permissions = ids
            const update = await this.updateRole(role)
            if( update.status ){
                this.methods.callRoles()
            }
        }catch(err){
            console.log(err)
        }
    }
    async wipeOutRole(){
        try{
            if( this.roleForm === "update" ){
                const result = await this.deleteRole(this.role)
                if( result.status ){
                    this.methods.callRoles()
                    this.methods.openFormCreateRol(false)
                }
            }
        }catch(err){
            console.log(err)
        }
    }
    async submitRole(){
        const role = {...this.role}
        const form = {...this.form}
        if( form.name.trim() === "" ){
            this.methods.alert(true, "El nombre del rol es un campo obligatorio", "error")
            return
        }
        if( this.roleForm === "update" ){
            const update = await this.updateRole({...role, ...form})
            if( update.status ){
                this.methods.callRoles()
                this.methods.openFormCreateRol(false)
                this.methods.selectRol({})
                return
            }
        }
        if(this.roleForm === "create" ){
            const update = await this.createRole({...form})
            if( update.status ){
                this.methods.callRoles()
                this.methods.openFormCreateRol(false)
                this.methods.selectRol({})
                return
            }
        }
    }
    async get(){
        const permissions = await app.get(`/get/all/permissions`)
        return permissions
    }
    async getRoles(){
        const roles = await app.get(`/get/roles`)
        return roles
    }
}