import constant from '../actions/index.js'

export const fieldForm = {
	name : ""
}

const initialState = {
	list: [],
	typeForm: "create",
	create_category: {
		isOpenForm: false,
		form: [{...fieldForm}]
	},
	update_category:{
		category_selected: {}
	},
	delete_categories:{
		multiple: false,
		categories: []
	},
}

export default function CategoriesReducer(state = initialState, action){
	const globalState = state
	switch(action.type){
		case constant.SAVE_LIST_OF_CATEGORIES :
			globalState.list = action.categories
		break;
		case constant.UPDATE_THE_TYPE_OF_CATEGORY_FORM :
			globalState.typeForm = action.typeForm
		break;
		case constant.IS_THE_SALE_OPEN_WITH_THE_CATEGORY_FORM :
			globalState.create_category.isOpenForm = action.value
		break;
		case constant.UPDATE_THE_CATEGORY_FORM :
			globalState.create_category.form = action.form
		break;
		case constant.SELECT_THIS_CATEGORY :
			globalState.update_category.category_selected = action.category_selected
		break;
		case constant.SELECT_CATEGORIES_TO_DELETE :
			globalState.delete_categories.categories = action.categories
		break;
		case constant.DELETE_MULTIPLE_CATEGORIES :
			globalState.delete_categories.multiple = action.value
		break;
		default :
			return globalState
	}

	return globalState
}