import React from 'react';
import PropTypes from 'prop-types';
import NumberFormat from 'react-number-format';

const TextFieldMask = (props) => {
    const { value="", inputRef, onChange, prefix="$", separator=" ", format=false, maxVal=0, mask, ...other} = props
    const withValueCap = (inputObj) => {
        const { value } = inputObj;
        if (value <= maxVal) return true;
        return false;
    };
    return (
        <NumberFormat
            value={value}
            format={format}
            allowNegative={false}
            {...other}
            getInputRef={inputRef}
            decimalScale={2}
            {...(Boolean(maxVal) && { isAllowed: withValueCap } )}
            decimalSeparator=","
            onValueChange={(values) => {
                onChange({
                    target: {
                        name: props.name,
                        value: values.value,
                    },
                });
            }}
            thousandSeparator={separator}
            isNumericString
            prefix={`${prefix} `}
            {...(Boolean(mask) && {mask})}
            
        />
    );
}

TextFieldMask.propTypes = {
    inputRef: PropTypes.func.isRequired,
    name: PropTypes.string.isRequired,
    onChange: PropTypes.func.isRequired,
};

export default TextFieldMask;