import React from 'react'
import { connect } from 'react-redux'
import Autocomplete from '../../../components/Autocomplete'
import { saveProducts } from '../../../redux/actions/ProductsAction'
import Product from '../_class'

const mapPropsToState = (state) => {
    return{
        list : state.Products.list,
    }
}

const mapDispatchToProps = (dispatch) => {
    return{
        saveProducts: (categories) => dispatch(saveProducts(categories)),
    }
}

const AutocompleteProducts = connect(mapPropsToState, mapDispatchToProps)(({ required=false, list, saveProducts, value={}, onChange }) => {
    const [page, setPage] = React.useState(1)
    const [match, setMatch] = React.useState("")
    const product = new Product({ 
        page,
        list,
        methods:{
            loadProductsToTheList: saveProducts,
        }
    })
    React.useEffect(() => {
        if( match.trim() !== "" ){
            setPage(1)
        }
        product.loadAutocompleteList(match)
    },[page, match])
    React.useEffect(() => {
        return () => {
            setPage(1)
            saveProducts([])
        }
    },[])
    return(
        <>
            <Autocomplete
                id="autocomplete-product-list"
                mb={2}
                onInputChange={(x,value) => setMatch(value)}
                required={required}
                label="Productos"
                options={list}
                onFetch={() => setPage(page+1)}
                value={value}
                name="product"
                onChange={ (e,a) => typeof onChange === "function" ? onChange({attr:e.attr, value:a}) : null }
            />
        </>
    )
})

export default AutocompleteProducts