import app from '../../connection/Api'
import history from '../../handlers/history'
import parseData, { addParamsToURL, decodeObject, deleteElement, encodeObject } from '../../methods/parseData'
import { isTheSaleOpenWithTheCategoryForm } from '../../redux/actions/CategoriesAction'
import store from '../../redux/store'
import { fieldForm as categoryFormData } from '../../redux/reducers/CategoriesReducer'
const dispatch = (action) => store.dispatch(action)

const methodsDefault = {
    alert : () => null,
    updateFormData : () => null,
    openForm : (value) => dispatch(isTheSaleOpenWithTheCategoryForm(value)),
    callCategories : (filter) => null,
    loadCategoriesToTheList : () => null,
}

export default class Categorie{
    constructor({ form=[], methods=methodsDefault, typeForm, categorySelected, page=1, limit=10, deletes=[], list=[] }){
        this.form = parseData(form)
        this.categorySelected = parseData(categorySelected)
        this.typeForm = typeForm
        this.methods = methods
        this.deletes = deletes
        this.page = page
        this.limit = limit
        this.list = parseData(list)
    }
    async get(filter={}){
        try{
            let url = addParamsToURL(`/get/categories?page=${this.page}&limit=${this.limit}&`, filter)
            let categories = await app.get(url)
            return categories
        }catch(error){
            console.log(error)
        }
    }
    async delete(){
        try{
            const encode = encodeObject(this.deletes)
            const response = await app.get(`/delete/categories?c=${encode}`)
            if( response.status === true ){
                const message = response.payload.success >= 2 ? "Las categorias fueron eliminados con exito": "La categoria fue eliminada con exito"
                this.methods.alert(true, message, "success")
                this.methods.callCategories({})
            }
        }catch(err){
            console.log(err)
        }
    }
    async create(categories){
        try{
            const response = await app.post('/save/categories', {categories} )
            if( response.status ){
                const message = response.payload.length >= 2 ? "Las categorías se crearon con éxito" : "La categoría se creó éxito"
                this.methods.alert(true, message , "success")
            }
            return response
        }catch(err){
            console.log(err)
        }
    }
    async update(categories){
        try{
            const response = await app.post('/update/categories', {categories} )
            if( response.status ){
                const message = response.payload.success >= 2 ? "Las categorías se actualizaron con éxito" : "La categoría se actualizo éxito"
                this.methods.alert(true, message , "success")
            }
            return response
        }catch(err){
            console.log(err)
        }
    }
    async submit(){
        const form = this.form
        let payload = []
        for( let index in form ){
            if( form[index].name.trim() !== "" ){
                payload.push(form[index])
            }
        }
        if( payload.length === 0 ){
            this.methods.alert(true, "Debes llenar los campos de almenos una categoría", "warning")
            return
        }
        if( this.typeForm === "create" ){
            const result = await this.create(payload)
            if( result.status ){
                await this.methods.callCategories()
                methodsDefault.openForm(false)
            }
        }
        if( this.typeForm === "update" ){
            let categoriesSelected = this.categorySelected
            payload = [{...categoriesSelected, ...this.form[0]}]
            const result = await this.update(payload)
            if( result.status ){
                await this.methods.callCategories()
                methodsDefault.openForm(false)
            }
        }
    }
    async loadAutocompleteList(match=""){
        try{
            let list = [...this.list]
            const result = await this.get({match})
            if( result.status ){
                let newList = [...list, ...result.payload]
                let hash = {}
                newList = newList.filter(o => hash[o._id] ? false : hash[o._id] = true)
                this.methods.loadCategoriesToTheList(newList)
            }
        }catch(err){
            console.log(err)
        }
    }
    loadData(){
        if( this.typeForm === "update" ){
            if( Boolean(this.categorySelected._id) ){
                this.methods.updateFormData([{name : this.categorySelected.name }])
            }
        }
    }
    updateOneCategoryToTheList(index, attr, value){
        const newList = [...this.form]
        newList[index][attr] = value
        this.methods.updateFormData(newList)
        return
    }
    addNewCategoryToTheList(){
        const newList = [...this.form, categoryFormData]
        if( this.form.length === 5 ){
            this.methods.alert(true, "has alcanzado el límite de categorías en el formulario", "warning")
            return
        }
        this.methods.updateFormData(newList)
        return
    }
    removeOneCategoryToTheList(index){
        const newList = deleteElement([...this.form], null, index)
        this.methods.updateFormData(newList)
        return
    }
    static menu(businessName){
        return [
            {
                title: "Crear categorias",
                onClick: () => dispatch(isTheSaleOpenWithTheCategoryForm(true))
            },{
                title: "Administrar categorias",
                onClick: () => history.push(`/${businessName}/manage/categories`)
            },
        ]
    }
}