import constant from '../actions/index'

const formRole = {
	name: ""
}

const initialState = {
	permissions : [],
	roles : [],
	create_role:{
		form: {...formRole},
		isOpenModal: false,
		type: "create",
	},
	update_role:{
		roleSelected: {}
	}
}

export default function RolesAndPermissionsReducer(state = initialState, action){
	const globalState = {...state}
	switch(action.type){
		case constant.LOAD_LIST_OF_PERMISSIONS:
			globalState.permissions = action.permissions
		break;
		case constant.LOAD_LIST_OF_ROLES:
			globalState.roles = action.roles
		break;
		case constant.OPEN_FORM_CREATE_ROL:
			globalState.create_role.isOpenModal = action.value
		break;
		case constant.UPDATE_FORM_TYPE:
			globalState.create_role.type = action.typeForm
		break;
		case constant.UPDATE_FORM_CREATE_ROL:
			globalState.create_role.form = action.form
		break;
		case constant.SELECT_ROL:
			globalState.update_role.roleSelected = action.role
		break;
		default :
			return globalState
	}

	return globalState
}