import React from 'react'
import { Box, Typography, makeStyles } from '@material-ui/core'
import clsx from 'clsx'

const useStyles = makeStyles(theme => ({
    fontStyles:{
        width: "100%",
        fontFamily: "Roboto",
        fontWeight: "bold",
        lineHeight: "1.75"
    },
    upperCase : {
        textTransform : "uppercase"
    },
    fontSizeSmall : {
        fontSize : 16
    },
    fontSizeNormal : {
        fontSize : 18
    },
    fontSizeText : {
        fontSize : 20
    },
    fontSizeModalTitle : {
        fontSize : 18
    }
}))

const Text = ({ text="", uppercase, fontSize=false, mb=2, ml=0, color="#181818", lineHeight=1.75, mt=0 }) => {
    const clasess = useStyles()

    const style = clsx(clasess.fontStyles, {
        [clasess.upperCase] : uppercase,
        [clasess.fontSizeNormal] : !fontSize,
        [clasess.fontSizeModalTitle] : fontSize === "modal-title",
        [clasess.fontSizeText] : fontSize === "text",
        [clasess.fontSizeSmall] : fontSize === "small",
    })

    return(
        <>
            <Box style={{ color }} display="flex" className={style} ml={ml} mt={mt} >
                {text}
            </Box>
        </>
    )
}

export default Text