import React from 'react';
import { Box, IconButton } from '@material-ui/core'
import Text from './Text'
import Tooltip from './Tooltips';

const TitleViews = ({ icon, onClick, title, tooltip, mb=0, toolbar }) => {
    return(
        <>
            <Box bgcolor="#FFFFFF" border={1} px={2} borderRadius={5} color="grey.400" my={1} mb={mb} display="flex" alignItems="center" >
                <Text text={title} uppercase />
                <Box display="flex" >
                    {toolbar}
                    <Tooltip title={tooltip} placement='bottom' >
                        <IconButton color="primary" onClick={() => typeof onClick === "function" ? onClick() : null} >
                            {icon}
                        </IconButton>
                    </Tooltip>
                </Box>
            </Box>
        </>
    )
}

export default TitleViews