import { Box, IconButton } from '@material-ui/core'
import React from 'react'
import { connect } from 'react-redux'
import ActionButtons from '../../../components/ActionButtons'
import { Customer, DeleteSVG } from '../../../components/icons'
import Searchbar from '../../../components/Searchbar'
import SecureDialog from '../../../components/SecureDialog'
import Table from '../../../components/Table'
import TitleViews from '../../../components/TitleViews'
import { activeViewDataCustomer, addCustomerToDeleteList, applyDataToFormCustomer, getCustomers, openFormCustomerData, selectedDataCustomer, updateCustomerForm } from '../../../redux/actions/CustomerActions'
import { callNotificationSnackbar, isOpenSecureDialog, updatePage } from '../../../redux/actions/DashboardAction'
import ViewCustomerData from '../view_customer'
import CustomerClass from '../class'

const mapPropsToState = (state) => {
    return{
        customerList: state.Customer.list,
        isOpenViewDataCustomer: state.Customer.viewCustomer.isOpenViewDataCustomer,
        customerSelected: state.Customer.viewCustomer.customerSelected,
        customersIDS: state.Customer.deleteCustomer.ids,
        page : state.Dashboard.pagination.page
    }
}

const mapDispatchToProps = (dispatch) => {
    return{
        getCustomers : (filter={}) => dispatch(getCustomers(filter)),
        applyDataToFormCustomer : (value) => dispatch(applyDataToFormCustomer(value)),
        openFormCustomerData : (value) => dispatch(openFormCustomerData(value)),
        updateCustomerForm : (value) => dispatch(updateCustomerForm(value)),
        activeViewDataCustomer : (value) => dispatch(activeViewDataCustomer(value)),
        selectedDataCustomer : (value) => dispatch(selectedDataCustomer(value)),
        isOpenSecureDialog : (value) => dispatch(isOpenSecureDialog(value)),
        updatePage : (page) => dispatch(updatePage(page)),
        addCustomerToDeleteList : (value) => dispatch(addCustomerToDeleteList(value)),
        callNotificationSnackbar : (active, message, severity) => dispatch(callNotificationSnackbar(active, message, severity)),
    }
}

const ListCustomer = connect(mapPropsToState, mapDispatchToProps)(({ customerList, isOpenViewDataCustomer, customerSelected, page, customersIDS, ...actions }) => {
    const { getCustomers, applyDataToFormCustomer, openFormCustomerData, updateCustomerForm, activeViewDataCustomer, selectedDataCustomer, updatePage, isOpenSecureDialog, addCustomerToDeleteList, callNotificationSnackbar } = actions
    const [match, setMatch] = React.useState("")
    const customer = new CustomerClass({
        methods:{
            alert: callNotificationSnackbar,
            callCustomers: getCustomers
        }
    })
    const actionsTable = customersIDS.length <= 1 ?  {
        field: ' ',
        headerName: ' ',
        renderCell : (params) => {
            return <ActionButtons
                disabled={false}
                onDeleted={() => {
                    addCustomerToDeleteList([params.id])
                    isOpenSecureDialog(true)
                }}
                onEdit={() =>{
                        applyDataToFormCustomer(params.row)
                        selectedDataCustomer(params.row)
                        updateCustomerForm(true)
                        openFormCustomerData(true)
                    }
                }
                onViewData={() => {
                    selectedDataCustomer(params.row)
                    activeViewDataCustomer(true)
                }}
            />
        },
        flex: 1,
    } : null
    const columns = [
        {
            field: 'fullname',
            headerName: 'Cliente',
            editable: false,
            flex : 1,
            renderCell : (params) => {
                // console.log(params)
                return(
                    <>{params.row.firstName} {params.row.lastName}</>
                )
            },
        },{
            field: 'documentNumber',
            headerName: 'Numero de documento',
            editable: false,
            flex : 1,
            renderCell : (params) => {
                // console.log(params)
                return(
                    <>{params.row.documentNumber}</>
                )
            },
        },{...actionsTable}
    ]

    React.useEffect(() => {
        const filter = { page, text: match }
        getCustomers(filter)
    },[page, match])
    React.useEffect(() => {
        if( match.trim() !== "" ){
            updatePage(1)
        }
    },[match])
    return(
        <>
            <TitleViews
                title="Lista de clientes"
                mb={2}
                tooltip="Precione para crear cliente"
                icon={<Customer />}
                onClick={() => openFormCustomerData(true)}
                toolbar={<>
                    {customersIDS.length >= 2 && 
                        <Box display="flex" alignItems="center" justifyContent="center" >
                            <IconButton color="primary" onClick={() => isOpenSecureDialog(true)} >
                                <DeleteSVG />
                            </IconButton>
                        </Box>
                    }
                </>}
            />
            <Searchbar
                onChange={(value) => setMatch(value)}
            />
            <Table
                columns={columns}
                rows={customerList}
                loading={true}
                checkboxSelection
                onSelection={(values) => {
                    addCustomerToDeleteList(values)
                }}
            />
            <ViewCustomerData
                isOpen={isOpenViewDataCustomer}
                close={activeViewDataCustomer}
                customerSelected={customerSelected}
                selectedDataCustomer={selectedDataCustomer}
            />
            <SecureDialog
                textButton="Eliminar cliente"
                onSubmit={() => customer.deletes(customersIDS)}
            />
        </>
    )
})

export default ListCustomer