import React from 'react'
import Media from 'react-media';
import { useTheme, Box, useMediaQuery } from '@material-ui/core'

const Responsive = ({ children, mediaFunctions={} }) => {
    const theme = useTheme()
    const xs = useMediaQuery(theme.breakpoints.down('xs'))
    const sm = useMediaQuery(theme.breakpoints.down('sm'))
    const md = useMediaQuery(theme.breakpoints.down('md'))
    const lg = useMediaQuery(theme.breakpoints.down('lg'))

    const isFunction = (fn) => {
        if(typeof fn === "function"){
            fn()
            return
        }else{
            return
        }
    }
    React.useEffect(() => {
        if( xs){
            isFunction(mediaFunctions.xs)
            return
        }
        if( sm ){
            isFunction(mediaFunctions.sm)
            return
        }
        if( md ){
            isFunction(mediaFunctions.md)
            return
        }
        if( lg ){
            isFunction(mediaFunctions.lg)
            return
        }
    },[xs, sm, md, lg])
    return(
        <>
            <Box component="span" >
                {children}
            </Box>
        </>
    )
}

export default Responsive