import React from "react"
import { DOMAIN_SERVER } from "../connection/Api"
import { serverSideImage } from "../methods/parseData"

export const usePhotoProfile = (photoProfile) => {
    const [profilePhoto, setProfilePhoto] = React.useState("")
    const photoCallback = React.useCallback( async () => {
        if( photoProfile !== "" ){
            const photo = await serverSideImage([photoProfile], DOMAIN_SERVER)
            if( photo.length >= 1 ){
                setProfilePhoto(URL.createObjectURL(photo[0]))
            }
        }
	},[photoProfile])

	React.useEffect(() => {
		photoCallback()
	},[photoCallback])

    return profilePhoto
}