import { Box, Grid, Typography } from '@material-ui/core';
import React from 'react'
import Stepper from '../../../components/Stepper'
import { svgImages } from '../../../components/icons';
    function getSteps() {
        return ['¿Cargar masiva de productos?', '¿Como cargar productos?', '¿Como actualizar productos?', '¿Como funcionan las columnas?'];
    }
    function getStepContent(step) {
        switch (step) {
            case 0:
                return(
                    <Grid container >
                        <Grid item xs={6} >
                            <Typography align='justify' >
                                <Box component="span" >
                                    La forma más rápida de cargar una gran cantidad de productos y/o hacer modificaciones de productos ya existentes es a través de un archivo <b>EXCEL (XLS)</b>,
                                    <b>para cargar archivos será necesaria una plantilla</b> que nosotros le proporcionaremos para este fin, <b>la primera fila que contiene los títulos de las columnas no 
                                    deben sufrir ningún tipo de alteración </b> ni tampoco las validaciones de las mismas, <b>esta plantilla es única para con el fin de cargar información al sistema, 
                                    </b> cualquier alteración de la plantilla que no sea la de suministrar la información requerida puede llevar a generar errores en la carga de los productos. <br />
                                </Box>
                            </Typography>
                        </Grid>
                        <Grid item xs={6} >
                            <Box p={5} display="flex" justifyContent="center" alignItems="center" >
                                <img style={{ width: "80%" }} src={svgImages.mExcel} alt="Excel" />
                            </Box>
                        </Grid>
                    </Grid>
                )
            case 1:
                return(
                    <Grid container >
                        <Grid item xs={6} >
                            <Typography align='justify' >
                                <Box component="span" >
                                    Para comenzar a subir sus productos de forma "masiva" debe desde cargar la plantilla AQUÍ, una vez tenga descargada y abierta la plantilla
                                    en su ordenador deberá suministrar la información de los productos en el orden que aparece allí. <b>Recuerde que la plantilla no debe sufrir 
                                    alteraciones ajenas a las de añadir la información requerida, </b> una vez haya terminado de cargar la información de sus productos, puede 
                                    proceder a subir la plantilla a la aplicación a través del botón Cargar archivo. La plantilla pasará por un proceso de validación donde se le 
                                    serán informados a través de notificaciones de los errores encontrados, al haber pasado la validación ya estará listo para Enviar la información.
                                </Box>
                            </Typography>
                        </Grid>
                        <Grid item xs={6} >
                            <Box p={5} display="flex" justifyContent="center" alignItems="center" >
                                <img style={{ width: "80%" }} src={svgImages.mExcel} alt="Excel" />
                            </Box>
                        </Grid>
                    </Grid>
                )
            case 2:
                return(
                    <Grid container >
                        <Grid item xs={6} >
                            <Typography align='justify' >
                                <Box component="span" >
                                    De vez en cuando necesitará actualizar la información de productos ya registrados, para esto recomendamos utilizar los campos Código de barras o Código SKU como identificadores unicos, ya que estos son irrepetibles y la aplicación no admite códigos duplicados, haciendo uso de esto podemos usarlo para actualizar algún producto ya existente, solamente deberemos añadir la información del producto en la plantilla junto con sus o su código para que este sea reconocido y actualizado, Teniendo en cuenta esto; debe tener precausion, ya que si envía un código errado y existente el producto que tiene asignado dicho código sufrirá los cambios por eso recomendamos leer bien los cambios antes de subir los datos.
                                </Box>
                            </Typography>
                        </Grid>
                        <Grid item xs={6} >
                            <Box p={5} display="flex" justifyContent="center" alignItems="center" >
                                <img style={{ width: "80%" }} src={svgImages.mExcel} alt="Excel" />
                            </Box>
                        </Grid>
                    </Grid>
                )
            case 3:
                return(
                    <Grid container >
                        <Grid item xs={6} >
                            <Typography align='justify' >
                                <Box component="span" >
                                    De vez en cuando necesitará actualizar la información de productos ya registrados, para esto recomendamos utilizar los campos Código de barras o Código SKU como identificadores unicos, ya que estos son irrepetibles y la aplicación no admite códigos duplicados, haciendo uso de esto podemos usarlo para actualizar algún producto ya existente, solamente deberemos añadir la información del producto en la plantilla junto con sus o su código para que este sea reconocido y actualizado, Teniendo en cuenta esto; debe tener precausion, ya que si envía un código errado y existente el producto que tiene asignado dicho código sufrirá los cambios por eso recomendamos leer bien los cambios antes de subir los datos.
                                </Box>
                            </Typography>
                        </Grid>
                        <Grid item xs={6} >
                            <Box p={5} display="flex" justifyContent="center" alignItems="center" >
                                <img style={{ width: "80%" }} src={svgImages.mExcel} alt="Excel" />
                            </Box>
                        </Grid>
                    </Grid>
                )
            default:
                return 'Unknown step';
        }
    }

const Instructions = () => {
    return(
        <>
            <Stepper getSteps={getSteps} getStepContent={getStepContent} />
        </>
    )
}

export default Instructions