import { Box, Grid, Paper } from '@material-ui/core'
import React from 'react'
import Dialog from '../../../components/Dialog'
import TextField from '../../../components/TextField'
import Button from '../../../components/Button'
import Customer from '../class'
import TextFieldMask from '../../../components/TextFieldMask'

const Form = ({openForm, form, isUpdateForm, customerSelected, ...actions}) => {
    const { openFormCustomerData, updateDataCustomerForm, restoreDataForm, callNotificationSnackbar, getCustomers, selectedDataCustomer } = actions
    const customer = new Customer({
        data: form,
        methods: {
            updateForm: updateDataCustomerForm,
            activeNotification: callNotificationSnackbar,
            handlerCustomerForm: openFormCustomerData,
        }
    })
    const submitForm = async (evt) => {
        evt.preventDefault()
        let getData = undefined
        if( !isUpdateForm ){
            const result = await customer.submitFormCustomer()
            getData = result
        }else{
            const result = await customer.submitFormCustomer('update', customerSelected)
            getData = result
        }
        if( getData ){
            getCustomers()
        }
    }
    React.useEffect(() => {
        if( !openForm ){
            restoreDataForm()
            selectedDataCustomer({})
        }
    },[openForm])

    return(
        <>
            <Dialog open={openForm} close={() => openFormCustomerData(false)} title={isUpdateForm ? 'actualizar datos del cliente' : 'crear cliente'} >
                <Paper variant='outlined'>
                    <form action='#' method='POST' onSubmit={submitForm} >
                        <Box p={3} >
                            <Grid container spacing={1} >
                                <Grid item xs={6} >
                                    <TextField
                                        label="Nombres"
                                        name="firstName"
                                        onChange={customer.updateFormAtributes}
                                        value={form.firstName}
                                        required
                                    />
                                </Grid>
                                <Grid item xs={6}>
                                    <TextField
                                        name="lastName"
                                        label="Apellidos"
                                        onChange={customer.updateFormAtributes}
                                        value={form.lastName}
                                        required
                                    />
                                </Grid>
                                <Grid item xs={6}>
                                    <TextField
                                        name="documentNumber"
                                        label="Numero de documento"
                                        onChange={customer.updateFormAtributes}
                                        value={form.documentNumber}
                                        required
                                        InputProps={{
                                            inputComponent : TextFieldMask,
                                        }}
                                        inputProps={{
                                            prefix : "",
                                            separator: "",
                                            value : form.documentNumber,
                                        }}
                                    />
                                </Grid>
                                <Grid item xs={6}>
                                    <TextField
                                        name="storeName"
                                        label="Nombre de la tienda fisica"
                                        onChange={customer.updateFormAtributes}
                                        value={form.storeName}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                    <TextField
                                        name="email"
                                        label="Correo electronico"
                                        onChange={customer.updateFormAtributes}
                                        value={form.email}
                                    />
                                </Grid>
                                <Grid item xs={6}>
                                    <TextField
                                        name="phoneNumber"
                                        label="Numero de telefono"
                                        onChange={customer.updateFormAtributes}
                                        value={form.phoneNumber}
                                        InputProps={{
                                            inputComponent : TextFieldMask,
                                        }}
                                        inputProps={{
                                            prefix : "",
                                            separator: "",
                                            value : form.phoneNumber,
                                        }}
                                    />
                                </Grid>
                                <Grid item xs={6}>
                                    <TextField
                                        name="localNumber"
                                        label="Numero de telefono local"
                                        onChange={customer.updateFormAtributes}
                                        value={form.localNumber}
                                        InputProps={{
                                            inputComponent : TextFieldMask,
                                        }}
                                        inputProps={{
                                            prefix : "",
                                            separator: "",
                                            value : form.localNumber,
                                        }}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                    <TextField
                                        name="address"
                                        label="Direccion del domicilio"
                                        onChange={customer.updateFormAtributes}
                                        value={form.address}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                    <TextField
                                        name="storeAddress"
                                        label="Direccion de la tienda fisica"
                                        onChange={customer.updateFormAtributes}
                                        value={form.storeAddress}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                    <Button size="large" >{isUpdateForm ? 'Actualizar datos' : 'crear cliente'}</Button>
                                </Grid>
                            </Grid>
                        </Box>
                    </form>
                </Paper>
            </Dialog>
        </>
    )
}

export default Form