import React from 'react'
import { Box, Grid, } from '@material-ui/core'
import TextField from '../../../components/TextField'
import Button from '../../../components/Button'
import ButtonManageForm from '../../../components/ButtonManageForm'

const CreateCategory = ({ category, form=[], typeForm, letter, ...actions }) => {
    const { isTheSaleOpenWithTheCategoryForm } = actions

	const submitCategories = (evt) => {
		evt.preventDefault()
		evt.stopPropagation()
        category.submit()
	}

	return(
            <form action="#" method="POST" onSubmit={submitCategories} >
                <Grid container spacing={1} alignItems="center" >
                    <>
                        {form.length >= 1 &&
                            form.map((item,index) => {
                                return(
                                    <React.Fragment key={index} >
                                        <Grid item xs={typeForm === "create" ? 10 : 12} >
                                            <TextField
                                                name="name"
                                                mb={1}
                                                label="Nombre"
                                                placeholder="Ej: Gaseosa"
                                                value={item.name}
                                                onChange={({attr,value}) => category.updateOneCategoryToTheList(index, attr, value)}
                                            />
                                        </Grid>
                                        {typeForm === "create" &&
                                            <Grid item xs={2} >
                                                <Box display="flex" mb={1} flexDirection="row">
                                                    { index >= 1 ?
                                                        <ButtonManageForm
                                                            type="remove"
                                                            title="Eliminar"
                                                            placement="right"
                                                            color="secondary"
                                                            onClick={() => category.removeOneCategoryToTheList(index)}
                                                        /> : 
                                                        <ButtonManageForm
                                                            title="Puede añadir hasta 5 categorias"
                                                            placement="bottom"
                                                            onClick={ () => category.addNewCategoryToTheList() }
                                                        />
                                                    }
                                                </Box>
                                            </Grid>
                                        }
                                    </React.Fragment>
                                )
                        }) }
                    </>
                    <Grid item xs={12} sm={6} md={6} lg={6}>
                        <Button
                            size='large'
                            variant="outlined"
                        > {typeForm === "create" ? `Crear categoría${letter}` : 'Guardar cambios'}  </Button>
                    </Grid>
                    <Grid item xs={12} sm={6} md={6} lg={6}>
                        <Button
                            type="button"
                            size='large'
                            variant="outlined"
                            color="secondary"
                            onClick={ () => isTheSaleOpenWithTheCategoryForm(false) }
                        >Cancelar</Button>
                    </Grid>
                </Grid>
            </form>
	)
}

export default CreateCategory