import React from 'react'
import { connect } from 'react-redux'
import { getCustomers, openFormCustomerData, restoreDataForm, selectedDataCustomer, updateDataCustomerForm } from '../../../redux/actions/CustomerActions'
import { callNotificationSnackbar } from '../../../redux/actions/DashboardAction'
import Form from './Form'

const mapPropsToState = (state) => {
    return{
        openForm: state.Customer.createCustomer.openForm,
        form: state.Customer.createCustomer.form,
        isUpdateForm: state.Customer.updateCustomer.isUpdateForm,
        customerSelected: state.Customer.viewCustomer.customerSelected,
    }
}

const mapDispatchToProps = (dispatch) => {
    return{
        openFormCustomerData: (value) => dispatch(openFormCustomerData(value)),
        updateDataCustomerForm: (value) => dispatch(updateDataCustomerForm(value)),
        restoreDataForm: () => dispatch(restoreDataForm()),
        selectedDataCustomer: (value) => dispatch(selectedDataCustomer(value)),
        getCustomers: (filter={}) => dispatch(getCustomers(filter)),
        callNotificationSnackbar: (active,message, severity) => dispatch(callNotificationSnackbar(active,message, severity)),
    }
}

const CreateCustomer = connect(mapPropsToState, mapDispatchToProps)(({openForm, form, isUpdateForm, customerSelected, ...actions}) => {
    const { openFormCustomerData, updateDataCustomerForm, restoreDataForm, callNotificationSnackbar, getCustomers, selectedDataCustomer } = actions
    return(
        <>
            <Form
                openForm={openForm}
                openFormCustomerData={openFormCustomerData}
                form={form}
                updateDataCustomerForm={updateDataCustomerForm}
                restoreDataForm={restoreDataForm}
                isUpdateForm={isUpdateForm}
                callNotificationSnackbar={callNotificationSnackbar}
                getCustomers={getCustomers}
                customerSelected={customerSelected}
                selectedDataCustomer={selectedDataCustomer}
            />
        </>
    )
})

export default CreateCustomer