import React from 'react'
import CreateVariantModal from '../views/variants/create_variant/CreateVariantModal'
import CreateUnitSize from '../views/unitSize/create_unitSize'
import LoadProducts from '../views/products/load_product'
import CreateCustomer from '../views/customers/create_customer'
import CreateBill from '../views/bills/create_bill'
import CreateCategory from '../views/categories/create_categories'


const PopupsModals = () => {
    return(
        <>
            <CreateCategory />
            <CreateVariantModal />
            <CreateUnitSize />
            <LoadProducts />
            <CreateCustomer />
            <CreateBill />
        </>
    )
}

export default PopupsModals