import React from 'react'
import { Box } from '@material-ui/core'
import Dialog from '../../../components/Dialog'
import { Sheets } from '../../../components/icons'
import LoadFile  from '../../../components/LoadFile'
import Button  from '../../../components/Button'
import { connect } from 'react-redux'
import { loadProductsFileWindow } from '../class'
import Product from '../_class'
import Instructions from './Instructions'
import { callNotificationSnackbar } from '../../../redux/actions/DashboardAction'

const mapPropsToState = (state) => {
    return {
        status : state.Products.loadProducts.isOpenModal
    }
}

const mapDispatchToProps = (dispatch) => {
    return{
        callNotificationSnackbar: (open,message,warning) => dispatch(callNotificationSnackbar(open,message,warning)),
    }
}

const LoadProducts = connect(mapPropsToState,mapDispatchToProps)(({status, ...actions}) => {
    const [tableData, setTableData] = React.useState(undefined)
    const { callNotificationSnackbar } = actions

    React.useEffect(() => {
        if( Boolean(tableData) ){
            callNotificationSnackbar(true, "La informacion esta lista para ser cargada", "success")
        }
    },[tableData])
    React.useEffect(() => {
        if( !status ){
            setTableData(undefined)
        }
    },[status])
    return(
        <>
            <Dialog fullScreen open={status} close={() => loadProductsFileWindow()} title="Cargar lista de productos" >
                <Box display="flex" width="100%" justifyContent="right" alignItems="center" >
                    <LoadFile
                        icon={<Sheets />}
                        accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
                        onReader={(data) => {
                            if(data.length <= 0){
                                setTableData(undefined)
                                return
                            }
                            const product = new Product({
                                xlsData: data,
                                methods: {
                                    activeAlert: callNotificationSnackbar
                                }
                            })
                            const list = product.validateXlsData()
                            setTableData(list)
                        }}
                    />
                    {Boolean(tableData) &&
                        <Box width="100%" maxWidth={100} ml={2} >
                            <Button color="secondary" >Enviar</Button>
                        </Box>
                    }
                </Box>
                <Instructions />
            </Dialog>
        </>
    )
})

export default LoadProducts