import React from 'react'
import { connect } from 'react-redux'
import ActionButtons from '../../../components/ActionButtons'
import { Bills } from '../../../components/icons'
import Searchbar from '../../../components/Searchbar'
import Table from '../../../components/Table'
import TitleViews from '../../../components/TitleViews'
import { currencyFormat } from '../../../methods/parseData'
import { isOpenModalFormBills, saveListBills, selectBill, updateBillFormType } from '../../../redux/actions/BillAction'
import Bill from '../class'
import ViewBill from '../view_bill'

const mapPropsToState = (state) => {
    return{
        list : state.Bills.list,
        page : state.Bills.page,
        billSelected : state.Bills.update_bill.billSelected
    }
}

const mapDispatchToProps = (dispatch) => {
    return{
        saveListBills: (list) => dispatch(saveListBills(list)),
        selectBill: (bill) => dispatch(selectBill(bill)),
        isOpenModalFormBills: (value) => dispatch(isOpenModalFormBills(value)),
        updateBillFormType: (value) => dispatch(updateBillFormType(value)),
    }
}

const ListBills = connect(mapPropsToState, mapDispatchToProps)(({list, page, billSelected, ...actions}) => {
    const [activeOverlay, setActiveOverlay ] = React.useState(true)
    const { saveListBills, selectBill, isOpenModalFormBills, updateBillFormType } = actions
    const columns = [
        {
            field: 'asunt',
            headerName: 'Asunto',
            editable: false,
            flex : 1,
        },{
            field: 'bill',
            headerName: 'Cantidad',
            editable: false,
            flex : 1,
            renderCell : (params) => {
                // console.log(params)
                return (
                    <>
                        $ {currencyFormat(params.value)}
                    </>
                )
            },
        },{
            field: ' ',
            headerName: ' ',
            renderCell : (params) => {
                return <ActionButtons
                onEdit={() => {
                    updateBillFormType("update")
                    selectBill(params.row)
                    isOpenModalFormBills(true)
                }}
                disabled={false}
            />
            },
            flex : 1,
        },
    ]
    const bill = new Bill({
        page,
        methods: {
            activeOverlayList: setActiveOverlay,
            saveList: saveListBills
        }
    })
    const callList = React.useCallback(() => {
        bill.loadList()
    },[page])
    React.useEffect(() => {
        callList()
    },[callList])
    return(
        <>
            <TitleViews
                mb={2}
                title="Gastos registrados"
                tooltip="Click para crear un nuevo gasto"
                onClick={() => isOpenModalFormBills(true)}
                icon={<Bills />}
            />
            <Searchbar />
            <Table
                rows={list}
                columns={columns}
                rowCount={10}
                page={page}
                loading={!activeOverlay}
                overlayText="Cargando gastos"
            />
            <ViewBill
                bill={billSelected}
            />
        </>
    )
})

export default ListBills