import React from 'react'
import { connect } from 'react-redux'
import Autocomplete from '../../../components/Autocomplete'
import { isTheSaleOpenWithTheCategoryForm, saveListOfCategories } from '../../../redux/actions/CategoriesAction'
import Categorie from '../class'
import { Grid, Box, useMediaQuery, useTheme } from '@material-ui/core'
import { CategoriesSVG } from '../../../components/icons'
import Tooltip from '../../../components/Tooltips'
import { IconButton } from '../../../components/Button'

const mapPropsToState = (state) => {
    return{
        list : state.Categories.list,
    }
}

const mapDispatchToProps = (dispatch) => {
    return{
        saveListOfCategories: (categories) => dispatch(saveListOfCategories(categories)),
        isTheSaleOpenWithTheCategoryForm: (categories) => dispatch(isTheSaleOpenWithTheCategoryForm(categories)),
    }
}

const AutocompleteCategory = connect(mapPropsToState, mapDispatchToProps)(({ required=true, list, saveListOfCategories, isTheSaleOpenWithTheCategoryForm, value={}, onChange }) => {
    const [page, setPage] = React.useState(1)
    const [match, setMatch] = React.useState("")
    const theme = useTheme()
    const xs = useMediaQuery(theme.breakpoints.down('xs'))

    const category = new Categorie({ 
        page,
        list,
        methods:{
            loadCategoriesToTheList: saveListOfCategories,
        }
    })
    React.useEffect(() => {
        if( match.trim() !== "" ){
            setPage(1)
        }
        category.loadAutocompleteList(match)
    },[page, match])
    React.useEffect(() => {
        return () => {
            setPage(1)
            saveListOfCategories([])
        }
    },[])
    return(
        <>
            <Grid container spacing={2} {...xs ? {direction: 'column-reverse'} : { alignItems:"center" } }  >
                <Grid item xs={12} sm={11} md={11} lg={11} >
                    <Autocomplete
                        mb={2}
                        onInputChange={(x,value) => setMatch(value)}
                        required={required}
                        label="Categorias"
                        options={list}
                        onFetch={() => setPage(page+1)}
                        value={value}
                        name="category"
                        onChange={ (e,a) => typeof onChange === "function" ? onChange({attr:e.attr, value:a}) : null }
                    />
                </Grid>
                <Grid item xs={12} sm={1} md={1} lg={1} >
                        <Tooltip placement='top' title="Crear nueva categoria" >
                            <Box mb={2}>
                                <IconButton variant="primary" onClick={() => isTheSaleOpenWithTheCategoryForm(true)} >
                                    <CategoriesSVG width="25px" />
                                </IconButton>
                            </Box>
                        </Tooltip>
                </Grid>
            </Grid>
        </>
    )
})

export default AutocompleteCategory