import React from 'react'
import Dialog from '../../../components/Dialog'
import { currencyFormat } from '../../../methods/parseData'
import { ShowData } from '../../products/view_product_data'

const ViewBill = ({ bill={} }) => {
    return(
        <>
            <Dialog open={false} close={() => null} title="Informacion del gasto" maxWidth='xs' >
                <ShowData title="Cantidad" text={`$ ${currencyFormat(bill.bill)}`} />
                <ShowData title="Asunto" text={bill.asunt} />
                <ShowData title="Nota" text={bill.note} />
            </Dialog>
        </>
    )
}

export default ViewBill