import React from 'react';
import { Box, Grid } from '@material-ui/core';
import TitleViews from '../../../components/TitleViews'
import BasicForm from './BasicForm';
import PriceForm from './PriceForm';
import StockForm from './StockForm';
import { ProductSVG } from '../../../components/icons'
import Button from '../../../components/Button';
import Product from '../_class';
// import Product from '../class';

const SubmitForm = ({localeText}) => {
    return(
        <Box my={2} >
            <Button size="large" type='submit' >{ localeText.buttonSubmit }</Button>
        </Box>
    )
}

const Form = ({ priceList, useStock, stock, stockAlert, variants, selectedVariants, variantPage, categories, unitSize, productBasicData, typePage, productIdentifier, sizeLeftBox, sizeRightBox, component, compount, images, listCompount, ...actions }) => {
    const [image, setImage] = React.useState([])
    const {
        loadProductData,
        updateProductPriceList,
        calculatePricePerPercentage,
        updateValueUseStock,
        updateProductForm,
        getCategories,
        callNotificationSnackbar,
        getUnitSize,
        restoreProductData,
        updateCompositeList
    } = actions

    const product = new Product({
        isCompount: compount,
        compountList: listCompount,
        typeRequest: typePage,
        _id: productIdentifier,
        productBasicData,
        priceList,
        files: image,
        stock:{ stock, stockAlert, useStock },
        methods: {
            activeAlert: callNotificationSnackbar,
            updatePriceList: updateProductPriceList,
            updateBasicData: updateProductForm,
            restoreForm: restoreProductData,
            restoreFormCompount: updateCompositeList,
        }
    })
    const localeText = Product.localeText(typePage, compount)
    const submitForm = (evt) => {
        evt.preventDefault()
        evt.stopPropagation()
        product.submit()
    }

    return(
        <>
            <TitleViews
                mb={2}
                title={localeText.title}
                tooltip={localeText.explicitForm}
                onClick={() => null}
                icon={<ProductSVG />}
            />
            <form method="POST" action="#" encType="multipart/form-data" onSubmit={submitForm} >
				<Grid container spacing={2} >
                    <Grid item xs={12} sm={12} md={sizeLeftBox} lg={sizeLeftBox} >
                        <BasicForm
                            categories={categories}
                            unitSize={unitSize}
                            getCategories={getCategories}
                            getUnitSize={getUnitSize}
                            productBasicData={productBasicData}
                            updateProductForm={updateProductForm}
                            setImage={setImage}
                            images={images}
                            callNotificationSnackbar={callNotificationSnackbar}
                            loadProductData={loadProductData}
                            onProduct={product}
                        />
                        <SubmitForm localeText={localeText} />
                    </Grid>
                    <Grid item xs={12} sm={12} md={sizeRightBox} lg={sizeRightBox} >
                        {component}
                        <PriceForm
                            priceList={priceList}
                            calculatePricePerPercentage={calculatePricePerPercentage}
                            compount={compount}
                            onProduct={product}
                            productBasicData={productBasicData}
                        />
                        <StockForm
                            useStock={useStock}
                            updateValueUseStock={updateValueUseStock}
                            stock={stock}
                            stockAlert={stockAlert}
                            updateProductForm={updateProductForm}
                            productBasicData={productBasicData}
                        />
                    </Grid>
                </Grid>
			</form>
        </>
    )
}

export default Form