import React from 'react'
import Dialog from '../../../components/Dialog'
import TextField from '../../../components/TextField'
import Button from '../../../components/Button'
import { Box, Grid, IconButton } from '@material-ui/core'
import { DeleteSVG } from '../../../components/icons'

const CreateRole = ({ isOpenModal, type, form={}, roleSelected, permissionsClass, ...actions }) => {
    const { openFormCreateRol, updateFormType, updateFormCreateRol, selectRol, isOpenSecureDialog } = actions
    React.useEffect(() => {
        if( type === "update" ){
            const data = {...form, name: roleSelected.name}
            updateFormCreateRol(data)
        }
    },[type])
    React.useEffect(() => {
        if( !isOpenModal ){
            const cleanForm = {...form, name: ""}
            updateFormCreateRol(cleanForm)
            if( type !== "create" ){
                selectRol({})
                updateFormType("create")
            }
        }
    },[isOpenModal])
    const sendForm = (evt) => {
        evt.preventDefault()
        evt.stopPropagation()
        permissionsClass.submitRole()
    }
    return(
        <>
            <Dialog open={isOpenModal} close={() => openFormCreateRol(false)} title={type === "create"? "Crear rol" : "Actualizar rol"} >
                <form onSubmit={sendForm} >
                    {type === "update" ? 
                    <Box mb={2} >
                        <IconButton color="primary" onClick={() => isOpenSecureDialog(true)} >
                            <DeleteSVG />
                        </IconButton>
                    </Box> : null}
                    <TextField
                        label="Nombre del rol"
                        name="name"
                        value={form.name}
                        onChange={({attr, value}) => updateFormCreateRol({...form, [attr]: value})}
                        required
                    />
                    <Box mb={2} >
                        <Grid container spacing={2} >
                            <Grid item xs={12} sm={6} md={6} lg={6} >
                                <Button size='large' variant="outlined" > {type === "create"? "Crear rol" : "Actualizar rol"} </Button>
                            </Grid>
                            <Grid item xs={12} sm={6} md={6} lg={6} >
                                <Button size='large' variant="outlined" type='button' color="secondary" onClick={() => openFormCreateRol(false)} >Cerrar</Button>
                            </Grid>
                        </Grid>
                    </Box>
                </form>
            </Dialog>
        </>
    )
}

export default CreateRole