import React, { useRef } from 'react';
import { Box, InputAdornment, makeStyles, Tooltip } from '@material-ui/core';
import SearchRoundedIcon from '@material-ui/icons/SearchRounded';
import ClearRoundedIcon from '@material-ui/icons/ClearRounded';
import TextField from './TextField';
import clsx from 'clsx';
import { IconButton } from './Button';

const useStyles = makeStyles(theme => ({
    searchIcon : {
        color : theme.palette.grey[400]
    },
    clearButton : {
        backgroundColor : theme.palette.primary.light,
        color : theme.palette.primary.main,
    }
}))

const Searchbar = ({ onChange, placeholder="Buscar..." }) => {
    const [isFocus, setIsFocus] = React.useState(false)
    const [text, setText] = React.useState('')
    const searchbar = useRef()
    const clasess = useStyles()
    let styles = clsx(clasess.clearButton)
    const handlerChange = (dat) => {
        setText(dat.value)
    }
    const clearText = () => {
        setText('')
        searchbar.current.focus()
    }
    React.useEffect(() => {
        if( typeof onChange === "function" ){
            onChange(text)
        }
    },[text])
    return(
        <Box bgcolor="#FFF" borderRadius={8} >
            <TextField
                onFocus={() => setIsFocus(true)}
                onBlur={() => setIsFocus(false)}
                ref={searchbar}
                value={text}
                placeholder={placeholder}
                onChange={handlerChange}
                InputProps={{
                    startAdornment :
                        <InputAdornment position='start' >
                            <Box {...isFocus ? {color: "primary.main"} : {className: clasess.searchIcon }} >
                                <SearchRoundedIcon />
                            </Box>
                        </InputAdornment>,
                    endAdornment : 
                        <InputAdornment position='end' >
                            { Boolean(text) &&
                                <Tooltip title="Limpiar" placement='left' >
                                    <Box>
                                        <IconButton variant='primary' onClick={clearText} >
                                            <ClearRoundedIcon />
                                        </IconButton>
                                    </Box>
                                </Tooltip>
                            }
                        </InputAdornment>
                }}
            />
        </Box>
    )
}

export default Searchbar