import React from 'react';
import { Grid, Paper, Box } from '@material-ui/core'
import Autocomplete from '../../../components/Autocomplete'
import TextField from '../../../components/TextField';
import RadioButton from '../../../components/RadioButton';
import Dropzone, { ImageGalerie } from '../../../components/Dropzone';
import parseData from '../../../methods/parseData'
import { DOMAIN_SERVER } from '../../../connection/Api';
import Product from '../class';
import Switch from '../../../components/Switch';
import { CustomizeTooltip } from '../../../components/Tooltips';
import TextFieldMask from '../../../components/TextFieldMask';
import AutocompleteCategory from '../../categories/list_categories/AutocompleteCategory';
import AutocompleteUnitSize from '../../unitSize/list_unitSize/AutocompleteUnitSize';

const BasicForm = ({categories, unitSize, productBasicData={}, images, ...actions}) => {
    const { getCategories, getUnitSize, updateProductForm, setImage, callNotificationSnackbar, loadProductData, onProduct } = actions
    const { status, category, name, skucode, providercode, unitSize : unitSizeValue, description, enableForSale, barcode } = productBasicData
    const product = new Product()
    React.useEffect(() => {
        getCategories()
        getUnitSize()
    },[])
    const handlerDataForm = ({attr, value}) => onProduct.updateBasicData(attr, value)
    return(
        <>
            <Paper elevation={0} variant="outlined" >
                <Box display="flex" flexDirection="column" p={2} >
                    <Grid container spacing={1} >
                        <Grid item xs={12} >
                            <Box display="flex" alignItems="center" justifyContent="space-between" >
                                <RadioButton
                                    value={status}
                                    label="Estado"
                                    required
                                    collection={[
                                        {label : "Nuevo", value : "new"},
                                        {label : "Usado", value : "use"},
                                    ]}
                                    onChange={handlerDataForm}
                                    name="status"
                                />
                                <CustomizeTooltip title={<b>Habilitar en POS y tienda</b>} >
                                    <Box>
                                        <Switch
                                            name="enableForSale"
                                            onChange={(value, attr) => handlerDataForm({attr, value})}
                                            checked={enableForSale}
                                        />
                                    </Box>
                                </CustomizeTooltip>
                            </Box>
                        </Grid>
                        <Grid item xs={12} >
                            <AutocompleteCategory
                                onChange={(val) => handlerDataForm(val)}
                                value={category}
                            />
                        </Grid>
                        <Grid item xs={12} >
                            <TextField
                                required
                                type="text"
                                onChange={handlerDataForm}
                                label="Nombre del producto"
                                value={name}
                                name="name"
                            />
                        </Grid>
                        <Grid item xs={6} >
                            <TextField
                                label="Codigo SKU"
                                onChange={handlerDataForm}
                                value={skucode}
                                name="skucode"
                            />
                        </Grid>
                        <Grid item xs={6} >
                            <TextField
                                label="Codigo proveedor"
                                onChange={handlerDataForm}
                                value={providercode??""}
                                name="providercode"
                                InputProps={{
                                    inputComponent : TextFieldMask
                                }}
                                inputProps={{
                                    prefix : "",
                                    value : providercode??"",
                                    separator: ""
                                }}
                            />
                        </Grid>
                        <Grid item xs={12} >
                            <TextField
                                label="Codigo de barras"
                                onChange={handlerDataForm}
                                value={barcode??""}
                                name="barcode"
                                InputProps={{
                                    inputComponent : TextFieldMask
                                }}
                                inputProps={{
                                    prefix : "",
                                    separator: "",
                                    value : barcode??"",
                                }}
                            />
                        </Grid>
                        <Grid item xs={12} >
                            <AutocompleteUnitSize
                                value={unitSizeValue}
                                onChange={(data) => handlerDataForm(data)}
                                renderOption={(options) => {
                                    const {compoundUnitOfMeasure, compoundUnit, name, baseUnit} = options
                                    const text = compoundUnitOfMeasure ? `${name} x${compoundUnit}${baseUnit.abbreviation}` : `${name}`
                                    return(
                                        <>
                                            {text}
                                        </>
                                    )
                                }}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                label="Descripcion del producto"
                                rows={4}
                                multiline
                                value={description}
                                onChange={handlerDataForm}
                                name="description"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <Dropzone
                                domain={DOMAIN_SERVER}
                                onChange={(imgCollection) => setImage(imgCollection.value)}
                                name="imagesProduct"
                                defaultImages={images}
                            />
                        </Grid>
                    </Grid>
                </Box>
            </Paper>
        </>
    )
}

export default BasicForm