import constant from ".";

export const isOpenModalFormBills = (value) => {
    return {type: constant.IS_OPEN_MODAL_FORM_BILLS, value}
}
export const updateFormBill = (form) => {
    return {type: constant.UPDATE_FORM_BILL, form}
}
export const restoreFormBill = () => {
    return {type: constant.RESTORE_FORM_BILL}
}
export const saveListBills = (list) => {
    return {type: constant.SAVE_LIST_BILLS, list}
}
export const selectBill = (bill) => {
    return {type: constant.SELECT_BILL, bill}
}
export const updateBillFormType = (value) => {
    return {type: constant.UPDATE_BILL_FORM_TYPE, value}
}