import React from 'react';
import { Box, Grid } from '@material-ui/core';
import ItemCard from '../../../components/ItemCard';
import { FooterItemCardActionsBills, FooterItemCardActionsCategories, FooterItemCardActionsCompountProducts, FooterItemCardActionsCustomers, FooterItemCardActionsOrders, FooterItemCardActionsProducts, FooterItemCardActionsReports, FooterItemCardActionsUnitSize, FooterItemCardActionsVariants, MenuManage } from './FooterItemCardActions';
import Categorie from '../../categories/class'
import { loadProductsFileWindow } from '../../products/class';
import Product from '../../products/_class';
import { connect } from 'react-redux';
import { useParams } from 'react-router-dom';
import { svgImages } from '../../../components/icons'
import { goCreateVariant, goManageVariants } from '../../variants/class';
import { goCreateUnitSize, goManageUnitSize } from '../../unitSize/class';
import Compount from '../../compountproducts/class';
import { goTableReports } from '../../reports/class';
import { getToday, getYesterday } from '../../../methods/Moment';
import history from '../../../handlers/history';
import { goManageOrders } from '../../orders/class';
import { goManageBusiness } from '../../customers/class';
import { openFormCustomerData } from '../../../redux/actions/CustomerActions';
import Bill from '../../bills/class';

const mapPropsToState = (state) => {
    return {
        categories: state.Categories.categories
    }
}

const mapDispatchToProps = (dispatch) => {
    return{
        openFormCustomerData : (value) => dispatch(openFormCustomerData(value))
    }
}

const ManageBusiness = connect(mapPropsToState, mapDispatchToProps)((props) => {
    const { categories, openFormCustomerData } = props
    const { businessName } = useParams()
    return(
        <Box>
            <Grid container spacing={2} >
                <ItemCard
                    title='Categorias'
                    icon={svgImages.categories}
                    actions={ <MenuManage menu={Categorie.menu(businessName)} /> }
                />
                <ItemCard
                    title='Productos'
                    icon={svgImages.product}
                    actions={
                        <FooterItemCardActionsProducts
                            manageProducts={() => Product.viewManageProducts(businessName)}
                            createProducts={() => Product.viewCreateProduct(businessName) }
                            loadProductFile={() => loadProductsFileWindow() }
                        />
                    }
                />
                <ItemCard
                    title='Productos compuestos'
                    icon={svgImages.compount}
                    actions={
                        <FooterItemCardActionsCompountProducts
                            manageCompountProducts={() => Compount.goManageCompountProduct(businessName)}
                            createCompountProducts={() => Compount.goCreateCompountProduct(businessName) }
                        />
                    }
                />
                <ItemCard
                    title='Unidades de medidas'
                    icon={svgImages.unitsType}
                    actions={<FooterItemCardActionsUnitSize
                        createUnitSize={() => goCreateUnitSize()}
                        manageUnitSize={() => goManageUnitSize(businessName)}
                    />}
                />
                <ItemCard
                    title='Clientes'
                    icon={svgImages.customer}
                    actions={ <FooterItemCardActionsCustomers
                        manageCustomers={() => goManageBusiness(businessName)}
                        createCustomer={() => openFormCustomerData(true)}
                    /> }
                />
                <ItemCard
                    title='Reportes de ventas'
                    icon={svgImages.reports}
                    actions={ <FooterItemCardActionsReports
                        manageReportsForTwoDays={() => goTableReports(businessName, getYesterday(), getToday())}
                    /> }
                />
                <ItemCard
                    title='Pedidos'
                    icon={svgImages.order}
                    actions={
                        <FooterItemCardActionsOrders
                            manageOrders={() => goManageOrders(businessName) }
                        />
                    }
                />
                <ItemCard
                    title='Gastos'
                    icon={svgImages.bills}
                    actions={
                        <FooterItemCardActionsBills
                            createBills={() => Bill.openForm()}
                            manageBills={() => Bill.goManageBills(businessName)}
                        />
                    }
                />
                <ItemCard
                    title='Vender'
                    icon={svgImages.cashRegister}
                    pointer
                    onClick={() => history.push(`${history.location.pathname}/market`)}
                />
            </Grid>
        </Box>
    )
})

export default ManageBusiness