import React from 'react'
import { connect } from 'react-redux'
import Autocomplete from '../../../components/Autocomplete'
import { isTheSaleOpenWithTheCategoryForm, saveListOfCategories } from '../../../redux/actions/CategoriesAction'
import { Grid, Box, useMediaQuery, useTheme } from '@material-ui/core'
import { UnitSize } from '../../../components/icons'
import Tooltip from '../../../components/Tooltips'
import { IconButton } from '../../../components/Button'
import UnitSizeClass from '../_class'

const mapDispatchToProps = (dispatch) => {
    return{
        saveListOfCategories: (categories) => dispatch(saveListOfCategories(categories)),
        isTheSaleOpenWithTheCategoryForm: (categories) => dispatch(isTheSaleOpenWithTheCategoryForm(categories)),
    }
}

const AutocompleteUnitSize = connect(null, mapDispatchToProps)(({ required=true, value={}, onChange, withUnitSize="", addButton=true, name="unitSize", renderOption }) => {
    const [label, setLabel] = React.useState("")
    const [page, setPage] = React.useState(1)
    const [match, setMatch] = React.useState("")
    const [list, setList] = React.useState([])
    const theme = useTheme()
    const xs = useMediaQuery(theme.breakpoints.down('xs'))
    const unitSizeType = /base|compount/.test(withUnitSize) ? withUnitSize : null
    const unitsize = new UnitSizeClass({ 
        page,
        list,
        methods:{
            loadUnitSizesToTheList: setList,
        }
    })
    React.useEffect(() => {
        unitsize.loadAutocompleteList(unitSizeType, match)
    },[page, unitSizeType, match])
    React.useEffect(() => {
        return () => {
            setPage(1)
            setList([])
        }
    },[])
    React.useEffect(() => {
        if( unitSizeType === "base" ) setLabel("Unidades de medida (base)")
        if( unitSizeType === "compount" ) setLabel("Unidades de medida (compuestas)")
        if( !unitSizeType ) setLabel("Unidades de medida")
    },[unitSizeType])
    return(
        <>
            <Grid container spacing={2} {...xs ? {direction: 'column-reverse'} : { alignItems:"center" } }  >
                <Grid item xs={12} sm={addButton ? 11 : 12} md={addButton ? 11 : 12} lg={addButton ? 11 : 12} >
                    <Autocomplete
                        {...renderOption && {renderOption}}
                        filterOptions={{stringify: option => `${option.name} ${option.abbreviation}`}}
                        onInputChange={(x,value) => setMatch(value)}
                        mb={2}
                        required={required}
                        label={label}
                        options={list}
                        onFetch={() => setPage(page+1)}
                        value={value}
                        name={name}
                        onChange={ (e,a) => typeof onChange === "function" ? onChange({attr:e.attr, value:a}) : null }
                    />
                </Grid>
                {addButton && 
                    <Grid item xs={12} sm={1} md={1} lg={1} >
                        <Tooltip placement='top' title="Crear nueva unidad de medida" >
                            <Box mb={2}>
                                <IconButton variant="primary" onClick={() => UnitSizeClass.goCreateUnitSize()} >
                                    <UnitSize width="25px" />
                                </IconButton>
                            </Box>
                        </Tooltip>
                    </Grid>
                }
            </Grid>
        </>
    )
})

export default AutocompleteUnitSize