import React from 'react'
import avatar from '../styles/svg/018-account.svg'
import { Avatar as Image } from '@material-ui/core';
import { usePhotoProfile } from '../hooks/usePhotoProfile';

const Avatar = ({src="", size="30px"}) => {
    const blob = usePhotoProfile(src)
    return(
        <>
            <Image alt="profile-photo" src={Boolean(blob) ? blob : avatar } style={{ height : size, width : size }} />
        </>
    )
}

export default Avatar