import { Box, Card, Divider } from '@material-ui/core'
import React from 'react'
import { connect } from 'react-redux'
import Avatar from '../../../components/Avatar'
import { Item } from '../../profile'
import MoneyRoundedIcon from '@material-ui/icons/MoneyRounded';
import HomeRoundedIcon from '@material-ui/icons/HomeRounded';

const mapPropsToState = (state) => {
    return{
        businessActive: state.authState.data.businessActive
    }
}

const SettingBusiness = connect(mapPropsToState, null)(({ businessActive }) => {
    const { logo, businessName, description, taxIdentificationNumber, address } = businessActive
    return(
        <Card variant='outlined' elevation={0} >
            <Box p={2} >
                <Box display="flex" flexDirection="column" alignItems="center" >
                    <Avatar src={logo} size={80} />
                    <Box display="flex" mt={1} alignItems="center" justifyContent="center" flexDirection="column" >
                        <Box fontWeight="500" fontSize="1.5rem" >
                            {businessName}
                        </Box>
                        <Box fontWeight="500" fontSize={12} color="grey.600" >
                            {description}
                        </Box>
                    </Box>
                </Box>
                <Box my={2} >
                    <Divider style={{ height: "2px", backgroundColor: "#000" }} />
                </Box>
                <Item title="Numero de identificacion fiscal" icon={<MoneyRoundedIcon />} data={taxIdentificationNumber}  />
                <Item title="Direccion" icon={<HomeRoundedIcon />} data={address}  />
            </Box>
        </Card>
    )
})

export default SettingBusiness