import React from 'react'
import { Box, Collapse, List, ListItem as Item, ListItemIcon, ListItemText, makeStyles, useTheme } from '@material-ui/core'
import { useLocation } from 'react-router-dom'
import clsx from 'clsx'

const useStyles = makeStyles((theme) => ({
    root: {
        padding: "0px 10px"
    },
    listItem: {
        backgroundColor: theme.palette.secondary.light,
        borderRadius: theme.shape.borderRadius,
    },
    listItemPseudo: {
        marginBottom: "5px",
        "&:hover" : {
            backgroundColor: theme.palette.secondary.light,
            borderRadius: theme.shape.borderRadius,
        }
    },
    listItemIcon: {
        color: theme.palette.secondary.main
    }
}))

export const ListCollapse = ({ list=[], style={} }) => {
    const { pathname } = useLocation()
    const clasess = useStyles()
    const defaultText = {
        fontWeight: "500",
        fontSize: "0.8",
        overflow: "hidden",
        whiteSpace: "nowrap",
        textOverflow: "ellipsis",
        color: "#000000"
    }
    const theme = useTheme()
    return(
        <>
            {list.length >= 1 &&
                list.map((item, index) => {
                    return(
                        <React.Fragment key={index} >
                            <Item>
                                <ListItemText
                                    primaryTypographyProps={{
                                        style: defaultText
                                    }}
                                    primary={item.title??""} />
                            </Item>
                            {item.children.length >= 1 &&
                                item.children.map((child, index) => {
                                    return(
                                        <React.Fragment key={index} >
                                            <Collapse in={true} timeout="auto" unmountOnExit>
                                                <List component="div" disablePadding className={clasess.root} style={style} >
                                                    <Item button className={clsx(clasess.listItemPseudo, {[clasess.listItem] : pathname === child.path || child.defaultActive })} onClick={() => typeof child.onClick === "function" ? child.onClick() : null } >
                                                        <ListItemIcon className={clasess.listItemIcon} >
                                                            {child.icon??""}
                                                        </ListItemIcon>
                                                        <ListItemText
                                                            primaryTypographyProps={{
                                                                style: {...defaultText, color: theme.palette.secondary.main}
                                                            }}
                                                            {...Boolean(child.helpperText)&& {secondary: <Box fontSize="10px" color="primary.main" fontWeight="700" >{child.helpperText}</Box>}}
                                                            primary={child.title}
                                                        />
                                                    </Item>
                                                </List>
                                            </Collapse>
                                        </React.Fragment>
                                    )
                                })
                            }
                        </React.Fragment>
                    )
                })
            }
      </>
    )
}

const ListItem = ({onClick, icon, text}) => {
    return(
        <Item button onClick={ () => typeof onClick === "function" && onClick()  }>
            {icon && <ListItemIcon> {icon} </ListItemIcon>}
			<ListItemText primary={text} />
		</Item>
    )
}

export default ListItem