import constant from '../actions/index'

const form = {
    bill: "",
    asunt: "",
    note: "",
}

const initialState = {
    list: [],
    page: 1,
    typeForm : "create",
    create_bill: {
        openModalForm: false,
        form: {...form}
    },
    update_bill:{
        billSelected: {}
    }
}

export default function BillReducer(state = initialState, action){
    const globalState = {...state}
    switch(action.type){
        case constant.SAVE_LIST_BILLS:
            globalState.list = action.list
        break
        case constant.IS_OPEN_MODAL_FORM_BILLS:
            globalState.create_bill.openModalForm = action.value
        break
        case constant.UPDATE_FORM_BILL:
            globalState.create_bill.form = action.form
        break
        case constant.RESTORE_FORM_BILL:
            globalState.create_bill.form = {...form}
        break
        case constant.SELECT_BILL:
            globalState.update_bill.billSelected = action.bill
        break
        case constant.UPDATE_BILL_FORM_TYPE:
            globalState.typeForm = action.value
        break
        default :
            return globalState
    }
     return globalState
}