import { Box, InputAdornment } from '@material-ui/core'
import React from 'react'
import { connect } from 'react-redux'
import { isOpenSecureDialog } from '../redux/actions/DashboardAction'
import Button from './Button'
import Dialog from './Dialog'
import TextField from './TextField'

const mapPropsToState = (state) => {
    return{
        username : state.authState.data.username,
        secureDialog : state.Dashboard.isOpenSecureDialog
    }
}

const mapDispatchToProps = (dispatch) => {
    return{
        isOpenSecureDialog: (value) => dispatch(isOpenSecureDialog(value))
    }
}

const SecureDialog = connect(mapPropsToState, mapDispatchToProps)(({ textButton="Ejecutar", message="Por seguridad escriba su nombre de usuario para confirmar esta acción.", onSubmit, username, secureDialog, isOpenSecureDialog }) => {
    const [color, setColor ] = React.useState("")
    const [text, setText] = React.useState("")
    const [block, setBlock ] = React.useState(true)
    const handlerSubmit = (evt) => {
        evt.preventDefault()
        evt.stopPropagation()
        if( !block ){
            if( typeof onSubmit === "function" ){
                onSubmit()
            }
            isOpenSecureDialog(false)
        }
    }
    React.useEffect(() => {
        if( text === username ){
            setBlock(false)
        }else{
            setBlock(true)
        }
    },[text])
    React.useEffect(() => {
        if( !secureDialog ){
            setText('')
        }
    },[secureDialog])
    return(
        <>
            <Dialog open={secureDialog} close={() => isOpenSecureDialog(false)} title="Mensaje del sistema" >
                <form onSubmit={handlerSubmit} >
                    <Box fontSize="1rem" fontWeight="bold" mb={2} >
                        {message} <br /> <code>{username}</code>
                    </Box>
                    <Box>
                        <TextField
                            value={text}
                            onChange={({value}) => setText(value)}
                            onFocus={() => setColor("primary.main")}
                            onBlur={() => setColor("")}
                            InputProps={{
                                startAdornment:
                                <InputAdornment position='start' >
                                    <Box color={color} >
                                        @
                                    </Box>
                                </InputAdornment>
                            }}
                            label="Nombre de usuario"
                        />
                    </Box>
                    <Box mb={2} >
                        <Button size='large' variant='outlined' disabled={block} > {textButton} </Button>
                    </Box>
                </form>
            </Dialog>
        </>
    )
})

export default SecureDialog