import React from 'react';
import Form from './Form';
import { connect } from 'react-redux'
import {  getCategories, isTheSaleOpenWithTheCategoryForm, updateTheCategoryForm, updateTheTypeOfCategoryForm } from '../../../redux/actions/CategoriesAction';
import Categorie from '../class';
import { callNotificationSnackbar } from '../../../redux/actions/DashboardAction';
import Dialog from '../../../components/Dialog';
import { fieldForm as formData } from '../../../redux/reducers/CategoriesReducer';

const mapPropsToState = state => {
	return{
        form : state.Categories.create_category.form,
        isOpenForm : state.Categories.create_category.isOpenForm,
        typeForm : state.Categories.typeForm,
        category_selected : state.Categories.update_category.category_selected,
        page : state.Dashboard.pagination.page
	}
}

const mapDispatchToProps = (dispatch) => {
    return {
        callNotificationSnackbar : (active, message, severity) => dispatch(callNotificationSnackbar(active, message, severity)),
        updateTheCategoryForm : (form) => dispatch(updateTheCategoryForm(form)),
        getCategories : () => dispatch(getCategories({})),
        isTheSaleOpenWithTheCategoryForm : (value) => dispatch(isTheSaleOpenWithTheCategoryForm(value)),
        updateTheTypeOfCategoryForm : (value) => dispatch(updateTheTypeOfCategoryForm(value)),
    }
}

const CreateCategory = connect(mapPropsToState, mapDispatchToProps)(({ form, typeForm, isOpenForm, category_selected, page, ...actions }) => {
    const { callNotificationSnackbar, updateTheCategoryForm, getCategories, isTheSaleOpenWithTheCategoryForm, updateTheTypeOfCategoryForm } = actions
    const letter = form.length === 1 ? "" : "s"
    const category = new Categorie({
        form,
        categorySelected: category_selected,
        typeForm: typeForm,
        page,
        methods:{
            alert: callNotificationSnackbar,
            updateFormData: updateTheCategoryForm,
            callCategories: getCategories,
        }
    })
    React.useEffect(() => {
        if( !isOpenForm ){
            updateTheCategoryForm([formData])
            if( typeForm === "update" ){
                updateTheTypeOfCategoryForm("create")
            }
        }else{
            if( typeForm === "update" ){
                category.loadData()
            }
        }
    },[typeForm, isOpenForm])
    return(
        <Dialog open={isOpenForm} close={() => isTheSaleOpenWithTheCategoryForm(false)} title={ typeForm === "create" ? `Crear categoría${letter}` : `Actualizar categoría${letter}`} >
            <Form
                typeForm={typeForm}
                category={category}
                form={form}
                isTheSaleOpenWithTheCategoryForm={isTheSaleOpenWithTheCategoryForm}
                letter={letter}
            />
        </Dialog>
    )
})

export default CreateCategory