import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import { AppBar, Toolbar, Box } from '@material-ui/core';
import MenuProfile from '../../../components/MenuProfile';
import Mails from '../../../components/Mails';
import Notifications from '../../../components/Notifications';
import { IconButton } from '../../../components/Button';
import MenuRoundedIcon from '@material-ui/icons/MenuRounded';
import Responsive from '../../../components/Responsive';

const useStyles = makeStyles((theme) => ({
  appBar: {
    zIndex : 1000,
    backgroundColor : "#FFFFFF",
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarMovile : {
    zIndex: theme.zIndex.drawer + 1,
  },
  appBarShift: {
    width: `calc(100% - ${300}px)`,
    marginLeft: 300,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  hide: {
    display: 'none',
  }
}));

const Navbar = ({openDrawe, setOpenDrawer}) => {
  const [anchorAppbar, setAnchorAppbar] = React.useState(true)
  const classes = useStyles();
  return (
    <Responsive 
      mediaFunctions={
        {
          xs: () => {
            setOpenDrawer(false)
            setAnchorAppbar(false)
          },
        }
      }
    >
      <AppBar elevation={0} position="fixed" className={ clsx(classes.appBar, { [classes.appBarShift]: openDrawe&&anchorAppbar, } ) } >
        <Toolbar variant="dense" >
          <Box display="flex" width="100%" >
            <IconButton variant='secondary' justify='right' onClick={() => setOpenDrawer(!openDrawe)} >
              <MenuRoundedIcon />
            </IconButton>
          </Box>
          <Box display="flex" justifyContent="end" width="100%" >
            <Box mx={1} >
              <Notifications />
            </Box>
            <Box mx={1} >
              <Mails />
            </Box>
            <Box mx={1}>
              <MenuProfile />
            </Box>
          </Box>
        </Toolbar>
      </AppBar>
    </Responsive>
  );
}


export default Navbar
