import { isOpenModalFormBills } from '../../redux/actions/BillAction'
import store from '../../redux/store'
import parseData, { createFormData, getObjectValueByName } from '../../methods/parseData'
import app from '../../connection/Api'
import history from '../../handlers/history'
const dispatch = (action) => store.dispatch(action)

const methodsDefault = {
    alert: () => null,
    restoreForm: () => null,
    closePopup: () => null,
    activeOverlayList: () => null,
    saveList: () => null,
}

export default class Bill{
    constructor({ billData={}, methods=methodsDefault, files=[], page=1, limit=10, billSelected }){
        this.billSelected = parseData(billSelected)
        this.billData = parseData(billData)
        this.methods = methods
        this.files = files
        this.page = page
        this.limit = limit
    }
    updateData(attr, value){
        const data = {...this.billData}
        data[attr] = value
        return data
    }
    validateFields(){
        const data = {...this.billData}
        if( Number.isNaN(parseFloat(data.bill)) || parseFloat(data.bill) <= 0 ){
            this.methods.alert(true, "El campo cantidad solo acepta datos numericos superiores a 0", "warning")
            return false
        }
        if( data.asunt.trim() === "" ){
            this.methods.alert(true, "Difina el asunto en el formulario de gastos", "warning")
            return false
        }

        return data
    }
    async submit(type){
        try{
            const regex = /^update|create/g.test(type)
            if( !regex ) throw "Error defined type submit, values valid create or update"
            const resultValidation = this.validateFields()
            if( resultValidation ){
                if( type === "create" ){
                    let payload = createFormData(resultValidation, this.files)
                    let result = await this.create(payload)
                    if( result.status ){
                        this.methods.restoreForm()
                        this.methods.closePopup(false)
                    }
                }
                if( type === "update" ){
                    let validateValue = Boolean(getObjectValueByName(this.billSelected, "_id"))
                    if( validateValue ){
                        let payload = createFormData({...this.billSelected,...resultValidation}, this.files)
                        let result = await this.update(payload)
                        if( result.status ) return
                    }else{
                        throw "There is no selected expense"
                    }
                }
            }
        }catch(err){
            console.log(err)
        }
    }
    async find(filter={}){
        try{
            let url = `/find/bills?page=${this.page}&limit=${this.limit}&`
            for(let key of Object.keys(filter) ){
                if( filter[key] === undefined ||  filter[key] === null ){
                    delete filter[key]
                }else{
                    url = url+`${key}=${filter[key]}&`
                }
            }
            const response = await app.get(url)
            if( response.status ){
                return response
            }else{
                if( Boolean(response.message) ){
                    this.methods.alert(true, response.message, "error")
                }
            }
        }catch(err){
            console.log(err)
        }
    }
    async loadList(){
        try{
            const result = await this.find()
            this.methods.activeOverlayList(!result.status)
            if( result.status ){
                this.methods.saveList(result.payload)
            }
        }catch(err){
            console.log(err)
        }
    }
    async create(payload){
        try{
            const response = await app.post('/save/bill', payload, false)
            if( response.status ){
                this.methods.alert(true, "El gasto fue registrado con exito", "success")
                return response
            }else{
                if( Boolean(response.message) ){
                    this.methods.alert(true, response.message, "error")
                }
            }
        }catch(err){
            console.log(err)
        }
    }
    async update(payload){
        try{
            const response = await app.post('/update/bill', payload, false)
            if( response.status ){
                this.methods.alert(true, "El gasto fue actualizado con exito", "success")
                return response
            }else{
                if( Boolean(response.message) ){
                    this.methods.alert(true, response.message, "error")
                }
            }
        }catch(err){
            console.log(err)
        }
    }
    static goManageBills(businessName){
        history.push(`/${businessName}/manage/bills`)
        return
    }
    static openForm(){
        dispatch(isOpenModalFormBills(true))
        return
    }
}