import React from 'react'
import { Box, Paper } from '@material-ui/core'
import Table from '../../../components/Table'
import Autocomplete from '../../../components/Autocomplete'
import { Start } from '../../../components/icons'
import { currencyFormat } from '../../../methods/parseData'
import UnitSize from '../../unitSize/_class'
import AutocompleteProducts from '../../products/list_products/AutocompleteProducts'

const TableCompountProduct = ({ inputValueProduct, compount, priceList, unitSize, onCompount, ...actions }) => {
    const { searchProductToProductCompount, selectProductFormComposite, openCompositeProductConfigurationWindow, editSelectedCompositeProduct, updateSelectedQuantity, updatePositionOfThePuttingUnit, assignMainProduct } = actions
    const columns = [
        {
            field: 'product.name',
            headerName: 'Producto',
            editable: false,
            flex : 1,
            renderCell : (params) => {
                // console.log(params.row.product)
                return(
                    <>
                        {params.row.main ? <Start width='20px' style={{ marginRight: "10px" }} /> : <></>}
                        {params.row.product.name}
                    </>
                )
            },
        },{
            field: 'quantity',
            headerName: 'Cantidad',
            editable: false,
            align: "center",
            renderCell : (params) => {
                const unitSize = UnitSize.getUnitSize(params.row.product.unitSize, params.row.positionUnitSize)
                return(
                    <>{`${params.row.quantity} (${unitSize.abbreviation})`}</>
                )
            },
        },{
            field: 'cost',
            headerName: 'Costo',
            editable: false,
            flex : 1,
            renderCell : (params) => {
                return(
                    <>{`$ ${currencyFormat(params.row.totalCost)}`}</>
                )
            },

        },
    ]
    React.useEffect(() => {
        onCompount.calcCostCompount(compount)
    },[ unitSize ])
    return(
        <>
            <Box mb={2}>
                <Paper variant='outlined' elevation={0} >
                    <Box p={1} >
                        <AutocompleteProducts
                            value={{}}
                            onChange={(data) => {
                                if( !Boolean(data.value._id) ){
                                    openCompositeProductConfigurationWindow(false)
                                    return
                                }
                                selectProductFormComposite(data.value)
                                openCompositeProductConfigurationWindow(true)
                            }}
                        />
                        {/* <Autocomplete
                            options={inputValueProduct}
                            label='Productos'
                            onInputChange={(event) => searchProductToProductCompount(event.target.value) }
                            onChange={(val, element) => {
                                val.value = element
                                if( val.value.length <= 0 ){
                                    val.value = {}
                                    openCompositeProductConfigurationWindow(false)
                                    return
                                }
                                selectProductFormComposite(val.value)
                                openCompositeProductConfigurationWindow(true)
                            }}
                        /> */}
                        <Table
                            getRowId={row => row.product._id}
                            hideFooter
                            pointer
                            onCellClick={(val) => {
                                const { row } = val
                                openCompositeProductConfigurationWindow(true)
                                editSelectedCompositeProduct(true)
                                selectProductFormComposite(row.product)
                                updateSelectedQuantity(row.quantity)
                                updatePositionOfThePuttingUnit(row.positionUnitSize)
                                assignMainProduct(row.main)
                            }}
                            // updatePage={(pg) => updateVariantsListPagePosition(pg)}
                            // page={position}
                            // rowCount={variantsTotal}
                            rows={compount}
                            columns={columns}
                            // overlayText="Cargando unidades de medidas"
                            loading={true}
                        />
                    </Box>
                </Paper>
            </Box>
        </>
    )
}

export default TableCompountProduct