import history from "../../handlers/history";
import parseData, { currencyFormat, deleteElement } from "../../methods/parseData";
import UnitSize from "../unitSize/_class";

const defaultMethods = {
    addCompountProduct: () => null,
    removeCompountProduct: () => null,
    closeWindowProductConfig: () => null,
    assignMainProduct: () => null,
    updatePriceList: () => null,
    alert: () => null,
}
export default class Compount{
    constructor({ methods=defaultMethods, openConfigModal, unitSize={}, compountSelected, positionUnitSize, quantity, mainProduct, compountList, priceList }){
        this.compountSelected = parseData(compountSelected);
        this.positionUnitSize = positionUnitSize;
        this.positionSalePrice = 0;
        this.quantity = quantity;
        this.openConfigModal = openConfigModal;
        this.mainProduct = mainProduct;
        this.compountList = parseData(compountList);
        this.unitSize = unitSize;
        this.methods = methods;
        this.priceList = parseData(priceList);
    }
    dataCompount(){
        if( this.openConfigModal ){
            const unitSize = UnitSize.getUnitSize(this.compountSelected.unitSize, this.positionUnitSize)
            let cost = unitSize.compoundUnitOfMeasure ? this.compountSelected.prices[0].cost : this.compountSelected.costMin
            let stock = unitSize.isBase ? this.compountSelected.stock : this.compountSelected.stock*unitSize.baseUnit.compoundUnit
            const total = cost*this.quantity
            let data = {
                name : this.compountSelected.name,
                cost: (currency=false) => currency ? currencyFormat(cost) : cost,
                total: (currency=false) => currency ? currencyFormat(total) : total,
                unitSizeInfoText : (type=0) => type === 0 ? unitSize.name : unitSize.abbreviation,
                stock: (currency=true) => currency ? currencyFormat(stock) : stock,
                useStock : this.compountSelected.useStock,
            }
            return data
        }else{
            return {}
        }
    }
    activeMainProduct(value){
        for(let item of this.compountList){
            if( item.main && value ){
                this.methods.alert(true, `Ya existe un producto base en la lista (${item.product.name})`, "warning")
                return
            }
        }
        this.methods.assignMainProduct(value)
    }
    addCompountToList(){
        if( this.openConfigModal ){
            const dataCompount = this.dataCompount()
            const newCompount = {product: {...this.compountSelected}, quantity: this.quantity, positionUnitSize: this.positionUnitSize, main: this.mainProduct, totalCost: dataCompount.total(false)}
            let list = [...this.compountList]
            const existProduct = list.length >= 1 ? list.find(p => p.product._id === this.compountSelected._id) : undefined
            if( Boolean(existProduct) ){
                for( let i in list ){
                    if( list[i].product._id === this.compountSelected._id ){
                        list[i] = newCompount
                        this.methods.alert(true, "Se actualizo el compuesto en la lista", "success")
                        this.methods.addCompountProduct(list)
                    }
                }
            }else{
                list.push(newCompount)
                this.methods.alert(true, "Se añadio el nuevo compuesto a la lista", "success")
                this.methods.addCompountProduct(list)
            }
            this.calcCostCompount(list)
            this.methods.closeWindowProductConfig(false)
        }
    }
    removeCompountToList(){
        const result = deleteElement(this.compountList, (item) => item.product._id === this.compountSelected._id )
        this.methods.alert(true, "Se removio el producto de la lista", "success")
        this.methods.addCompountProduct(result)
        this.methods.closeWindowProductConfig(false)
    }
    calcCostCompount(list){
        if( list.length >= 1 ){
            let newCost = 0
            for(let item of list){
                newCost += item.totalCost
            }
            for(let item of list){
                if( item.main && Boolean(this.unitSize._id) ){
                    const unitSize = UnitSize.getUnitSize(item.product.unitSize, item.positionUnitSize)
                    const unitSizeTotal =  unitSize.compoundUnit*item.quantity
                    const formula = (newCost/unitSizeTotal)*this.unitSize.compoundUnit
                    newCost = formula
                }
            }
            this.priceList[0].cost = newCost
            this.methods.updatePriceList(this.priceList)
        }
    }
    static goCreateCompountProduct(businessName){
        history.push(`/${businessName}/create/compount/product`)
    }
    static goManageCompountProduct(businessName){
        history.push(`/${businessName}/manage/compount/product`)
    }
}