import React from 'react'
import { connect } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { Avatar, Box, MenuItem, IconButton, Menu, Divider } from '@material-ui/core';
import avatar from '../styles/svg/018-account.svg'
import Button from './Button';
import SettingsRoundedIcon from '@material-ui/icons/SettingsRounded';
import { ListCollapse } from './ListItem';
import { usePhotoProfile } from '../hooks/usePhotoProfile';
import StoreMallDirectoryRoundedIcon from '@material-ui/icons/StoreMallDirectoryRounded';
import Profile from '../views/profile/class';

const mapPropsToState = (state) => {
	return{
		username : state.authState.data.username,
		photoProfile : state.authState.data.photoProfile,
		role : state.authState.data.role,
		name : state.authState.data.name,
		lastname : state.authState.data.lastname,
	}
}

const MenuProfile = connect(mapPropsToState, null)(({username, photoProfile, name, lastname, role}) => {
	const photo = usePhotoProfile(photoProfile)
	const [anchorEl, setAnchorEl] = React.useState(null);
	const open = Boolean(anchorEl);
	const history = useHistory()
	const handleMenu = (event) => {
		setAnchorEl(event.currentTarget);
	};
	const handleClose = () => {
		setAnchorEl(null);
	};
	return(
		<>
			<Box display="flex" flexDirection="row" alignItems="center" >
				<IconButton style={{ padding : '0px' }} aria-controls="menu-appbar" aria-haspopup="true" onClick={handleMenu} >
					<Avatar alt="profile-photo" src={Boolean(photo) ? photo : avatar } style={{ height : "30px", width : "30px" }} />
				</IconButton>
			</Box>
			<Menu id="menu-appbar" anchorEl={anchorEl} keepMounted open={open} onClose={handleClose}
				anchorOrigin={{
					vertical: 'top',
					horizontal: 'right',
				}}
				transformOrigin={{
					vertical: 'top',
					horizontal: 'right',
				}}
			>
				<Box p={2} >
					<b>Hola,</b> {name} {lastname}
					<Box fontSize="0.8rem" color="grey.400" >
						{role.name}
					</Box>
					<Box component="div" my={1} > <Divider /> </Box>
					<ListCollapse
						style={{padding: "0px"}}
						list={[{
							title: "",
							children: [
								{
									icon: <StoreMallDirectoryRoundedIcon />,
									title: "Crear sucursal",
									path: `/`,
									onClick: () => null,
								},{
									icon: <SettingsRoundedIcon />,
									title: "Configurar cuenta",
									path: `/setting/profile`,
									onClick: () => Profile.goSettigProfile(),
								}
							]
						}]}
					/>
					<Box component="div" my={1} > <Divider /> </Box>
					<Button
						size="large"
						special='primary'
						onClick={ _=> {
							localStorage.removeItem('user_token')
							history.push('/')
							// window.location.reload() //development
							window.location.href = "https://kike-eapv.github.io/system" //production
						}}
					>Desconectarse</Button>
					<MenuItem onClick={handleClose}> @{username} </MenuItem>
				</Box>
			</Menu>
		</>
	)
})

export default MenuProfile