import { Box, Grid } from '@material-ui/core'
import React from 'react'
import Dialog from '../../../components/Dialog'
import TextField from '../../../components/TextField'
import Dropzone from '../../../components/Dropzone'
import Button from '../../../components/Button'
import TextFieldMask from '../../../components/TextFieldMask'
import { connect } from 'react-redux'
import { isOpenModalFormBills, restoreFormBill, updateBillFormType, updateFormBill } from '../../../redux/actions/BillAction'
import Bill from '../class'
import { callNotificationSnackbar } from '../../../redux/actions/DashboardAction'

const mapPropsToState = (state) => {
    return{
        openModalForm : state.Bills.create_bill.openModalForm,
        form : state.Bills.create_bill.form,
        typeForm : state.Bills.typeForm,
        billSelected : state.Bills.update_bill.billSelected
    }
}
const mapDispatchToProps = (dispatch) => {
    return{
        isOpenModalFormBills : (value) => dispatch(isOpenModalFormBills(value)),
        updateFormBill : (value) => dispatch(updateFormBill(value)),
        callNotificationSnackbar : (active, message, severity) => dispatch(callNotificationSnackbar(active, message, severity)),
        restoreFormBill : () => dispatch(restoreFormBill()),
        updateBillFormType : (value) => dispatch(updateBillFormType(value)),
    }
}

const CreateBill = connect(mapPropsToState, mapDispatchToProps)(({ openModalForm, form, typeForm, billSelected, ...actions }) => {
    const { isOpenModalFormBills, updateFormBill, callNotificationSnackbar, restoreFormBill, updateBillFormType } = actions
    const [files, setFiles ] = React.useState([])
    const bill = new Bill({
        billSelected,
        billData: form,
        files,
        methods: {
            alert: callNotificationSnackbar,
            restoreForm: restoreFormBill,
            closePopup: isOpenModalFormBills,
        }
    })
    const updateForm = ({attr, value}) => {
        const newForm = bill.updateData(attr, value)
        updateFormBill(newForm)
    }
    const submitForm = (evt) => {
        evt.preventDefault()
        evt.stopPropagation()
        bill.submit(typeForm)
    }
    React.useEffect(() => {
        if( typeForm === "update" ){
            updateFormBill({
                bill: billSelected.bill,
                asunt: billSelected.asunt,
                note: billSelected.note,
            })
        }
    },[typeForm])
    React.useEffect(() => {
        if( !openModalForm ){
            updateBillFormType("create")
            restoreFormBill()
        }
    },[openModalForm])
    return(
        <>
            <Dialog open={openModalForm} close={() => isOpenModalFormBills(false)} title={typeForm === "create" ? 'Crear un nuevo gasto': 'Actualizar gasto'} maxWidth='sm' >
                <form onSubmit={submitForm} >
                    <Grid container spacing={2} >
                        <Grid item xs={12} >
                            <TextField
                                mb={0}
                                name="bill"
                                value={form.bill}
                                label="Cantidad:"
                                onChange={updateForm}
                                required
                                InputProps={{
                                    inputComponent : TextFieldMask,
                                }}
                                inputProps={{
                                    value: form.bill
                                }}
                            />
                        </Grid>
                        <Grid item xs={12} >
                            <TextField
                                mb={0}
                                name="asunt"
                                value={form.asunt}
                                onChange={updateForm}
                                label="Asunto:"
                                required
                            />
                        </Grid>
                        <Grid item xs={12} >
                            <TextField
                                mb={0}
                                name="note"
                                value={form.note}
                                label="Notas:"
                                onChange={updateForm}
                                multiline
                                rows={5}
                            />
                        </Grid>
                        <Grid item xs={12} >
                            Comprobantes:
                            <Dropzone
                                onChange={(data) => setFiles(data.value)}
                            />
                        </Grid>
                        <Grid item xs={12} >
                            <Box mb={1} >
                                <Button variant="outlined" size="large" >{typeForm === "create" ? 'Crear gasto': 'Actualizar gasto'}</Button>
                            </Box>
                            <Box mb={2}>
                                <Button variant="outlined" color="secondary" size="large" type="button" onClick={() => isOpenModalFormBills(false)} > Cerrar </Button>
                            </Box>
                        </Grid>
                    </Grid>
                </form>
            </Dialog>
        </>
    )
})

export default CreateBill