import { addParamsToURL } from "../../methods/parseData"
import app from "../../connection/Api"
import store from "../../redux/store"
import { activeModalToCreateUnitOfMeasure } from "../../redux/actions/UnitSizeAction"
const methodsDefault = {
    loadUnitSizesToTheList: () => null,
}

export default class UnitSize{
    constructor({ limit=10, page=1, list=[], methods=methodsDefault }){
        this.list = list
        this.limit = limit
        this.page = page
        this.methods = methods
    }
    async get(filter={}){
        try{
            let url = addParamsToURL(`/get/unit/size?page=${this.page}&limit=${this.limit}&`, filter)
            let unitSizes = await app.get(url)
            return unitSizes
        }catch(err){
            console.log(err)
        }
    }
    async loadAutocompleteList(isCompount="", match=""){
        try{
            let list = [...this.list]
            const result = await this.get({isCompount, match})
            if( result.status ){
                let newList = [...list, ...result.payload]
                let hash = {}
                newList = newList.filter(o => hash[o._id] ? false : hash[o._id] = true)
                this.methods.loadUnitSizesToTheList(newList)
            }
        }catch(err){
            console.log(err)
        }
    }
    static getUnitSize(unitSize={}, position=0){
        let unitSizeResult = {
            name: "",
            abbreviation: "",
            compoundUnit: "",
            compoundUnitOfMeasure: "",
            isBase: "",
            baseUnit: {
                name : "",
                abbreviation: "",
                compoundUnit: "",
            },
            calculeStock: false
        }
        if( !Boolean(unitSize._id) || typeof position !== "number" || position >= 2 || position < 0 ){
            return unitSizeResult
        }
        if(unitSize.compoundUnitOfMeasure){
            if( position === 0 ){
                unitSizeResult.name = unitSize.name
                unitSizeResult.abbreviation = unitSize.abbreviation
                unitSizeResult.baseUnit.name = unitSize.baseUnit.name
                unitSizeResult.baseUnit.abbreviation = unitSize.baseUnit.abbreviation
                unitSizeResult.baseUnit.compoundUnit = 1
                unitSizeResult.compoundUnit = unitSize.compoundUnit
                unitSizeResult.compoundUnitOfMeasure = true
                unitSizeResult.isBase = false
                unitSizeResult.calculeStock = false
                return unitSizeResult
            }
            if(position === 1){
                unitSizeResult.name = unitSize.baseUnit.name
                unitSizeResult.abbreviation = unitSize.baseUnit.abbreviation
                unitSizeResult.baseUnit.name = unitSize.name
                unitSizeResult.baseUnit.abbreviation = unitSize.abbreviation
                unitSizeResult.baseUnit.compoundUnit = unitSize.compoundUnit
                unitSizeResult.compoundUnit = 1
                unitSizeResult.compoundUnitOfMeasure = false
                unitSizeResult.isBase = false
                unitSizeResult.calculeStock = true
                return unitSizeResult
            }
        }else{
            unitSizeResult.name = unitSize.name
            unitSizeResult.abbreviation = unitSize.abbreviation
            unitSizeResult.baseUnit.name = ""
            unitSizeResult.baseUnit.abbreviation = ""
            unitSizeResult.baseUnit.compoundUnit = ""
            unitSizeResult.compoundUnit = 1
            unitSizeResult.compoundUnitOfMeasure = false
            unitSizeResult.isBase = true
            unitSizeResult.calculeStock = false
            return unitSizeResult
        }
    }
    static goCreateUnitSize(){
        const dispatch = (action) => store.dispatch(action)
        dispatch(activeModalToCreateUnitOfMeasure(true))
    }
}