import React from 'react';
import { connect } from 'react-redux';
import Table from '../../../components/Table';
import ActionButtons from '../../../components/ActionButtons'
import { deleteMultipleCategories, getCategories, isTheSaleOpenWithTheCategoryForm, selectCategoriesToDelete, selectThisCategory, updateTheTypeOfCategoryForm } from '../../../redux/actions/CategoriesAction';
import { CategoriesSVG, DeleteSVG } from '../../../components/icons'
import Searchbar from '../../../components/Searchbar';
import Categorie from '../class';
import TitleViews from '../../../components/TitleViews';
import SecureDialog from '../../../components/SecureDialog';
import { callNotificationSnackbar, isOpenSecureDialog, updatePage } from '../../../redux/actions/DashboardAction';
import Tooltip from '../../../components/Tooltips';
import { Box, IconButton } from '@material-ui/core';

const mapPropsToState = (state) => {
    return {
        list : state.Categories.list,
        deleteCategories : state.Categories.delete_categories.categories,
        multiple : state.Categories.delete_categories.multiple,
        page : state.Dashboard.pagination.page,
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        getCategories : (filter) => dispatch(getCategories(filter)),
        selectThisCategory : (value) => dispatch(selectThisCategory(value)),
        updateTheTypeOfCategoryForm : (value) => dispatch(updateTheTypeOfCategoryForm(value)),
        isTheSaleOpenWithTheCategoryForm : (value) => dispatch(isTheSaleOpenWithTheCategoryForm(value)),
        isOpenSecureDialog : (value) => dispatch(isOpenSecureDialog(value)),
        selectCategoriesToDelete : (value) => dispatch(selectCategoriesToDelete(value)),
        updatePage : (value) => dispatch(updatePage(value)),
        deleteMultipleCategories : (value) => dispatch(deleteMultipleCategories(value)),
        callNotificationSnackbar : (active, message, severity) => dispatch(callNotificationSnackbar(active, message, severity)),
    }
}

const ListCategories = connect(mapPropsToState, mapDispatchToProps)(({list, deleteCategories, page, multiple, ...actions }) => {
    const { selectThisCategory, getCategories, updateTheTypeOfCategoryForm, isTheSaleOpenWithTheCategoryForm, isOpenSecureDialog, selectCategoriesToDelete, updatePage, deleteMultipleCategories } = actions
    const category = new Categorie({
        deletes: deleteCategories,
        methods:{
            callCategories: getCategories,
            alert: callNotificationSnackbar,
        }
    })
    const [match, setMatch ] = React.useState("")
    const deleteOption = multiple
    const columns = [
        {
            field: 'name',
            headerName: 'Categoria',
            editable: false,
            flex : 1,
        },{
            field: ' ',
            headerName: ' ',
            renderCell : (params) => {
                return <ActionButtons
                onEdit={ () =>{
                    updateTheTypeOfCategoryForm("update")
                    selectThisCategory(params.row)
                    isTheSaleOpenWithTheCategoryForm(true)
                }}
                onDeleted={() => {
                    deleteMultipleCategories(false)
                    selectCategoriesToDelete([params.id])
                    isOpenSecureDialog(true)
                }}
                view={false}
                disabled={false}
                deleted={!deleteOption}
                edit={!deleteOption}
            />
            },
        },
    ]
    React.useEffect(() => {
        if( match.length >= 1 ){
            updatePage(1)
        }
        getCategories({match})
    },[page, match])

    return(
        <>
            <TitleViews
                title="Lista de categorías"
                tooltip="Crear nueva categoría"
                onClick={() => Categorie.menu()[0].onClick()}
                icon={<CategoriesSVG />}
                mb={2}
                toolbar={
                    deleteOption &&
                    <Box>
                        <Tooltip title={"Borrar categorias"} placement='bottom'>
                            <IconButton color="secondary" onClick={() => isOpenSecureDialog(true)} >
                                <DeleteSVG />
                            </IconButton>
                        </Tooltip>
                    </Box>
                }
            />
            <Searchbar
                onChange={(text) => setMatch(text)}
            />
            <Table
                checkboxSelection
                onSelection={(category) => {
                    deleteMultipleCategories(category.length >= 1 ? true : false)
                    selectCategoriesToDelete(category)
                }}
                rows={list}
                columns={columns}
                rowCount={10}
                loading={true}
            />
            <SecureDialog
                onSubmit={() => category.delete() }
                textButton="Eliminar categorias"
            />
        </>
    )
})

export default ListCategories