import { Box, Card, Divider, ListItem, ListItemIcon, ListItemText, IconButton, useMediaQuery, useTheme } from '@material-ui/core'
import React from 'react'
import { connect } from 'react-redux'
import Avatar from '../../components/Avatar'
import { usePhotoProfile } from '../../hooks/usePhotoProfile'
import PersonRoundedIcon from '@material-ui/icons/PersonRounded';
import AlternateEmailRoundedIcon from '@material-ui/icons/AlternateEmailRounded';
import PhoneAndroidRoundedIcon from '@material-ui/icons/PhoneAndroidRounded';
import WcRoundedIcon from '@material-ui/icons/WcRounded';
import EditRoundedIcon from '@material-ui/icons/EditRounded';
import VpnKeyRoundedIcon from '@material-ui/icons/VpnKeyRounded';

const mapPropsToState = (state) => {
    return{
        userData: state.authState.data,
    }
}

export const Item = ({title="", data="", icon=""}) => {
    const theme = useTheme()
    const isSmall = useMediaQuery(theme.breakpoints.down('xs'))
    const text = <Box fontSize={14} fontWeight={500} color="primary.main" display="flex" flexDirection="row" alignItems="center" >
                    {data}
                    <Box ml={3} >
                        <IconButton size="small" color="primary" >
                            <EditRoundedIcon />
                        </IconButton>
                    </Box>
                </Box>
    return(
        <ListItem >
            <ListItemIcon style={{ minWidth: 35 }} > <Box color="secondary.main" > {icon} </Box> </ListItemIcon>
            <ListItemText primary={<Box fontSize={14} fontWeight={500} >{title}</Box>} {...isSmall && {secondary: text}} />
            { !isSmall && text }
        </ListItem>
    )
}

const Profile = connect(mapPropsToState, null)(({userData}) => {
    const { photoProfile, name, lastname, role, username, email, phoneNumber, sex } = userData
    return(
        <>  
            <Card variant='outlined' elevation={0} >
                <Box p={2} >
                    <Box display="flex" flexDirection="row" >
                        <Avatar src={photoProfile} size={50} />
                        <Box display="flex" ml={2} alignItems="left" justifyContent="center" flexDirection="column" >
                            <Box fontWeight="500" >
                                {name} {lastname}
                            </Box>
                            <Box fontWeight="500" fontSize={12} color="grey.600" >
                                {role.name}
                            </Box>
                        </Box>
                    </Box>
                    <Box my={2} >
                        <Divider style={{ height: "2px", backgroundColor: "#000" }} />
                    </Box>
                    <Box>
                        <Item title='Nombre de usuario' data={`@${username}`} icon={<PersonRoundedIcon />} />
                        <Item title='Correo electronico' data={email} icon={<AlternateEmailRoundedIcon />} />
                        <Item title='Numero de telefono' data={phoneNumber} icon={<PhoneAndroidRoundedIcon />} />
                        <Item title='Genero' data={sex === "male" ? "Masculino" : "Femenino"} icon={<WcRoundedIcon />} />
                        <Item title='Contraseña' data={"********************"} icon={<VpnKeyRoundedIcon />} />
                    </Box>
                    <Box my={2} >
                        <Divider style={{ height: "2px", backgroundColor: "#000" }} />
                    </Box>
                </Box>
            </Card>
        </>
    )
})

export default Profile