import app from "../../connection/Api";
import history from "../../handlers/history";
import { formatDate } from "../../methods/Moment";
import parseData, { getObjectValueByName, deleteElement, createFormData, currencyFormat, addParamsToURL } from "../../methods/parseData";
import { priceList as priceTemplate } from '../../redux/reducers/ProductsReducer'
import UnitSize from "../unitSize/_class";

const defaultMethods = {
    activeAlert: () => null,
    updatePriceList: () => null,
    updateBasicData: () => null,
    restoreForm: () => null,
    restoreFormCompount: () => null,
    loadProductsToTheList: () => null,
}

export default class Product{
    constructor({ _id, data, list=[],xlsData=[], methods=defaultMethods, viewData, typeRequest, productBasicData, files, priceList, isCompount, compountList, stock={}, page=1, limit=10 }){
        this.list = list
        this._id = _id
        this.data = data
        this.viewData = viewData
        this.typeRequest = typeRequest
        this.isCompount = isCompount
        this.compountList = parseData(compountList)
        this.productBasicData = parseData(productBasicData)
        this.priceList = parseData(priceList)
        this.stock = parseData(stock)
        this.files = files
        this.methods = methods
        this.page = page
        this.limit = limit
        this.xlsData = parseData(xlsData)
        this.form = {
            _id: _id,
            ...productBasicData,
            prices: priceList,
            stock: stock.stock,
            stockAlert: stock.stockAlert,
            useStock: stock.useStock,
        }
    }
    async create(payload){
        try{
            let product = await app.post('/save/product', payload, false)
            return product
        }catch(err){
            console.log(err)
        }
    }
    async update(payload){
        try{
            let product = await app.post('/update/products', payload, false)
            return product
        }catch(err){
            console.log(err)
        }
    }
    async sendRequest(payload){
        try{
            if( this.typeRequest === "create" ){
                const resultCreate = await this.create(payload)
                if( resultCreate.status ){
                    this.methods.activeAlert(true, "Se registro el producto con exito", "success")
                    this.methods.restoreForm()
                    if( this.isCompount ) this.methods.restoreFormCompount([])
                    return
                }
            }
            if( this.typeRequest === "update" ){
                const resultUpdate = await this.update(payload)
                if( resultUpdate.status ){
                    this.methods.activeAlert(true, "Se actualizo el producto con exito", "success")
                    return
                }
            }
        }catch(err){
            console.log(err)
        }
    }
    async submit(){
        try{
            let product = {...this.form}
            let category = getObjectValueByName(product.category, "_id")
            let unitSize = getObjectValueByName(product.unitSize, "_id")
            if(this.isCompount){
                if( this.compountList.length <= 1 ){
                    this.methods.activeAlert(true, "Debe añadir por lo menos 2 productos a la lista", "warning")
                    return
                }
                product.isCompount = this.isCompount
                product.compount = this.compountList
            }
            if( !Boolean(category) ){
                this.methods.activeAlert(true,"Seleccione una categoria","warning")
                return
            }
            if( !Boolean(unitSize) ){
                this.methods.activeAlert(true,"Seleccione una unidad de medida","warning")
                return
            }
            if( product.name.trim() === "" ){
                this.methods.activeAlert(true,"Defina un nombre para el producto","warning")
                return
            }
            for(let price of product.prices){
                if( product.prices[0].cost <= 0 || typeof product.prices[0].cost !== "number" ){
                    this.methods.activeAlert(true,"Defina el costo del producto","warning")
                    return
                }
                if( typeof price.salePrice === "number" && product.prices[0].cost > price.salePrice ){
                    this.methods.activeAlert(true,"El costo no debe ser mayor al precio de venta","warning")
                    return
                }
            }
            product.category = category
            product.unitSize = unitSize
            let payload = undefined
            let x = undefined
            if(this.typeRequest === "update"){
                x = [product]
            }else{
                delete product._id
                x = product
            }
            payload = createFormData(x, this.files)
            await this.sendRequest(payload)
        }catch(err){
            console.log(err)
        }
    }
    async get(filter){
        try {
            let url = addParamsToURL(`/get/products?page=${this.page}&limit=${this.limit}&`, filter)
            const products = await app.get(url);
            return products
        } catch (error) {
            console.log(error)
        }
    }
    async loadAutocompleteList(text=""){
        try {
            let list = [...this.list]
            const result = await this.get({text})
            if( result.status ){
                let newList = [...list, ...result.payload]
                let hash = {}
                newList = newList.filter(o => hash[o._id] ? false : hash[o._id] = true)
                this.methods.loadProductsToTheList(newList)
            }
        } catch (error) {
            console.log(error)
        }
    }
    async updateBasicData(attr, value){
        try{
            const basicData = {...this.productBasicData}
            if( attr === "skucode" || attr === "barcode" ){
                if( typeof value === "string" && value.trim() !== ""){
                    const result = await this.get({[attr]: value})
                    if( result.status ){
                        let codetype = attr === "skucode" ? "SKU" : "de barras"
                        this.methods.activeAlert(true, `El codigo ${codetype} ${value} ya se encuentra registrado con el producto (${result.payload[0].name}), el codigo ${codetype} debe ser unico`, "warning")
                    }
                }
            }
    
            if( attr === "unitSize" ){
                const result = this.calcCostForUnitMeasure(value)
                basicData.costMin = result
            }
            basicData[attr] = value
            this.methods.updateBasicData('productBasicData', basicData)
        }catch(err){
            console.log(err)
        }
    }
    static validateSalePrice(prices=[]){
        let salePrices = []
        for(let price of prices){
            if( !Number.isNaN(parseFloat(price.salePrice)) ){
                salePrices.push(price)
            }
        }
        return salePrices
    }
    static calcStockForUnitSize(stock=0, unitsize={}){
        if( unitsize.calculeStock ){
            return unitsize.baseUnit.compoundUnit*stock
        }else{
            return stock
        }
    }
    static addPercentage(percentage, value){
        const calc = value+(percentage/100)*value
        return parseFloat(calc.toFixed(2))
    }
    static calcProfit(salePrice, cost){
        if(typeof salePrice === "number" && typeof cost === "number"){
            if( salePrice > cost ){
                return Boolean(salePrice-cost) ? parseFloat(Number(salePrice-cost).toFixed(2)) : ""
            }else{
                return ""
            }
        }
    }
    static calcProfitPercentaje(profit, cost){
        if(typeof cost === "number" && typeof profit === "number"){
                let result = (100 / cost ) * profit
                return Boolean(result) ? parseFloat(Number(result).toFixed(2)) : ""
        }
    }
    static calcSalePrice(cost, profitPercentaje){
        if(typeof cost === "number" && typeof profitPercentaje === "number"){
            let result = cost + (profitPercentaje/100*cost)
            return Boolean(result) ? parseFloat(Number(result).toFixed(2)) : ""
        }
    }
    static viewCreateProduct(businessName){
        history.push(`/${businessName}/create/product`)
    }
    static viewManageProducts(businessName){
        history.push(`/${businessName}/manage/products`)
    }
    static localeText(typeForm, isCompount){
        if( isCompount ){
            switch (typeForm) {
                case "update":
                    return {
                        title: "Editar informacion del producto compuesto",
                        explicitForm:  "Formulario para actualizar datos del producto compuesto",
                        buttonSubmit:  "Actualizar producto compuesto",
                        successMessage:  "Los datos del producto compuesto se actualizaron con exito",
                        errorMessage:  "Ha ocurrido un error al intentar actualizar la informacion del compuesto",
                    }
                default:
                    return {
                        title:  "Crear producto compuesto",
                        explicitForm:  "Formulario para añadir nuevo producto compuesto",
                        buttonSubmit:  "Crear producto compuesto",
                        successMessage:  "El producto compuesto se registro con exito",
                        errorMessage:  "Ha ocurrido un error al intentar registrar el producto compuesto",
                    }
            }
        }else{
            switch (typeForm) {
                case "update":
                    return {
                        title:  "Editar informacion del producto",
                        explicitForm:  "Formulario para actualizar datos del producto",
                        buttonSubmit:  "Actualizar producto",
                        successMessage:  "Los datos del producto se actualizaron con exito",
                        errorMessage:  "Ha ocurrido un error al intentar actualizar la informacion del producto",
                    }
                default:
                    return {
                        title:  "Crear producto",
                        explicitForm:  "Formulario para añadir nuevos productos",
                        buttonSubmit:  "Crear producto",
                        successMessage:  "El producto se registro con exito",
                        errorMessage:  "Ha ocurrido un error al intentar registrar el producto",
                    }
            }
        }
    }
    selectAsSellingPrice(index, value){
        let priceList = this.priceList
        if( value ){
            for( let i in priceList ){
                if( priceList[i].favoriteSalePrice ){
                    priceList[i].favoriteSalePrice = false
                    this.methods.activeAlert(true,`El precio ${priceList[index].identifier} se selecciono como precio de venta favorito`, "success")
                }
            }
        }else{
            this.methods.activeAlert(true,`Debe tener seleccionado un precio favorito`, "warning")
            return
        }
        priceList[index].favoriteSalePrice = value
        this.methods.updatePriceList(priceList)
    }
    updatePriceListValues(index, attr, value){
        let priceList = this.priceList
        if( attr !== "identifier" && attr !== "favoriteSalePrice" ) value = parseFloat(value)
        
        if( attr === "salePrice" || attr === "percentageProfit" ){
            if(!Boolean(priceList[0].cost)){
                this.methods.activeAlert(true, "Primero debe ingresar el costo del producto", "warning")
                return
            }
        }

        priceList[index][attr] = value

        if( attr === "cost" ){
            const result = this.calcCostForUnitMeasure(this.productBasicData.unitSize)
            this.productBasicData.costMin = result
            this.methods.updateBasicData('productBasicData', this.productBasicData)
        }

        if(attr === "cost" || attr === "salePrice" || attr === "percentageProfit"){
            const cost = parseFloat(priceList[0].cost)
            const salePrice = parseFloat(priceList[index].salePrice)
            const percentage = parseFloat(priceList[index].percentageProfit)
            if( salePrice >= cost || percentage >= 1){
                if( attr === "percentageProfit" || attr === "cost" ){
                    const saleTotal = Product.calcSalePrice(cost, percentage)
                    const profit = Product.calcProfit(saleTotal, cost)
                    priceList[index].salePrice = saleTotal
                    priceList[index].profit = profit
                }else if(attr === "salePrice" || attr === "cost"){
                    const profit = Product.calcProfit(salePrice, cost)
                    const profitPercentaje = Product.calcProfitPercentaje(profit, cost)
                    priceList[index].percentageProfit = profitPercentaje
                    priceList[index].profit = profit
                }
            }
        }

        this.methods.updatePriceList(priceList)
    }
    addPrice(){
        if( this.priceList.length <= 4 ){
            let newPriceList = [...this.priceList]
            const prices = {...priceTemplate}
            prices.identifier = `Precio ${newPriceList.length+1}`
            delete prices.cost
            newPriceList.push({...prices, favoriteSalePrice: false})
            this.methods.updatePriceList(newPriceList)
        }else{
            this.methods.activeAlert(true, "Ha alcanzado el limite de precios en la lista", "warning")
            return false
        }
    }
    removePrice(index){
        const newPriceList = deleteElement(this.priceList,null,index)
        this.methods.updatePriceList(newPriceList)
    }
    calcCostForUnitMeasure(unitMeasure){
        if( this.priceList[0].cost >= 1 && Object.keys(unitMeasure).length > 1){
            if(Boolean(unitMeasure._id)){
                if( unitMeasure.compoundUnitOfMeasure ){
                    let result = parseFloat(Number((this.priceList[0].cost/unitMeasure.compoundUnit)).toFixed(2)) 
                    return result
                }else{
                    return this.priceList[0].cost
                }
            }else{
                return 0
            }
        }else{
            return 0
        }
    }
    reviewData(){
        if( this.viewData ){
            let unitSize = UnitSize.getUnitSize(this.data.unitSize, 0)
            return {
                status: this.data.status === "new" ? "Nuevo" : "Usado",
                name: this.data.name??"",
                skucode: this.data.skucode??"",
                providercode: this.data.providercode??"",
                barcode: this.data.barcode??"",
                category: this.data.category.name??"",
                baseUnitSize: `${unitSize.isBase ? unitSize.name : unitSize.baseUnit.name}`,
                fullUnitSize: `${unitSize.name} (${unitSize.abbreviation}) x${unitSize.compoundUnit} ${unitSize.baseUnit.name}`,
                stock: `${currencyFormat(this.data.stock)} ${unitSize.name} x${unitSize.compoundUnit}`,
                stockAlert: Boolean(this.data.stockAlert) ? `${this.data.stockAlert} ${unitSize.name} x${unitSize.compoundUnit}` : "",
                useStock: this.data.useStock ? "Si" : "No",
                isCompount: this.data.isCompount ? "Es un producto compuesto" : "No es un producto compuesto",
                enableForSale: this.data.enableForSale??"",
                costMin: `$ ${currencyFormat(this.data.costMin)} x${unitSize.isBase ? unitSize.name : unitSize.baseUnit.name}`,
                enableForSale: this.data.enableForSale??"",
                enableForSale: this.data.enableForSale??"",
                prices: this.data.prices??[],
                description: this.data.description??"",
                createdAt: formatDate(this.data.createdAt, "DD/MM/YYYY"),
                compount: this.data.compount??[],
                images: this.data.images??[],
            }
        }else{
            return {}
        }
    }
    formatPriceXLS(xlsRow, indexList){
        const item = parseData(xlsRow)
        const keys = Object.keys(item)
        const list = [{},{},{},{},{}]
        const cost = item["Costo"]
        const row = 1+(parseInt(indexList)+1)
        for(let index in keys){
            const prices = /^[1-5]{1,1}-[A-Z a-z 0-9]+/g.exec(keys[index])
            if( Boolean(prices) ){
                const { input } = prices
                const positionList = parseInt(input.charAt(0))-1
                if( positionList === 0 ){
                    if( cost <= 0 || String(cost).trim() === "" ){
                        this.methods.activeAlert(true,`ERROR EN LA FILA ${row} - la columna Costo es un campo obligatorio. Corrija el error y vuelva a cargar el archivo.`,"error")
                        return false
                    }
                    list[positionList].cost = cost
                }
                if( input === `${positionList+1}-Identificador` ){
                    if( item[input].trim() === "" ) item[input] = "Precio 1"
                    list[positionList].identifier = item[input]
                }
                if( input === `${positionList+1}-Precio de venta` ){
                    if(cost > item[input]){
                        this.methods.activeAlert(true,`ADVERTENCIA EN LA FILA ${row} - la columna ${input} no debe ser menor al valor del Costo de la misma fila, esto puede causar problemas en los calculos de ganancias`,"warning")
                        return false
                    }
                    list[positionList].salePrice = item[input]
                }
                if( input === `${positionList+1}-Usar como favorito` ){
                    if( item[input] >= 2 || item[input] < 0){
                        this.methods.activeAlert(true,`ERROR EN LA FILA ${row} - la columna ${input} solo admite 1 (uno) para SI y 0 (cero) para NO como valores. Corrija el error y vuelva a cargar el archivo`,"error")
                        return false
                    }
                    list[positionList].favoriteSalePrice = Boolean(item[input])
                }
                if( Boolean(list[0].cost) && Boolean(list[positionList].salePrice) ){
                    const profit = Product.calcProfit(list[positionList].salePrice, list[0].cost)
                    const profitPercentaje = Product.calcProfitPercentaje(profit, list[0].cost)
                    list[positionList].percentageProfit = profitPercentaje
                    list[positionList].profit = profit
                }
                
            }
        }
        return list.filter( x => Object.keys(x).length >= 3 )
    }
    validateXlsData(){
        const data = [...this.xlsData]
        if( data.length >= 1 ){
            const listProduct = []
            for(let index in data){
                const listSchema = {
                    status: "new",
                    prices: [],
                    category: "",
                    barcode: "",
                    name: "",
                    skucode: "",
                    providercode: "",
                    unitSize: "",
                    description:"",
                    stock: "",
                    stockAlert: "",
                    costMin: "",
                    isCompount: false,
                    compount: [],
                }
                const prices = this.formatPriceXLS(data[index], index)
                if( !prices ) return false
                if( String(data[index]["Nombre del producto"]).trim() === "" ){
                    this.methods.activeAlert(true,`ERROR EN LA FILA ${parseInt(index)+1}- El Campo Nombre del producto es un campo obligatorio. Corrija el error y vuelva a cargar el archivo`,"error")
                    return false
                }
                if( typeof data[index]["Codigo de la categoria"] !== "number" || typeof data[index]["Codigo de unidad de medida"] !== "number" ){
                    this.methods.activeAlert(true,`ERROR EN LA FILA ${parseInt(index)+1}- Los campos Codigo de unidad de medida o Codigo de la categoria son campos exclusivamente nunericos y obligarios. Corrija el error y vuelva a cargar el archivo`,"error")
                    return false
                }
                listSchema.status = data[index]["Estado del producto"] === "Nuevo" ? "new" : "use"
                listSchema.name = data[index]["Nombre del producto"]
                listSchema.category = data[index]["Codigo de la categoria"]
                listSchema.skucode = data[index]["Codigo SKU"]
                listSchema.providercode = data[index]["Codigo del proveedor"]??""
                listSchema.barcode = data[index]["Codigo de barras"]
                listSchema.unitSize = data[index]["Codigo de unidad de medida"]
                listSchema.description = data[index]["Descripcion"]??""
                listSchema.stock = data[index]["Disponible"]??0
                listSchema.stockAlert = data[index]["Stock bajo"]??0
                listSchema.prices = prices

                listProduct.push(listSchema)
            }
            return listProduct
        }
    }
}