import { Box, Divider, IconButton, Paper, Typography } from '@material-ui/core';
import React from 'react';
import TextField from '../../../components/TextField';
import TextFieldMask from '../../../components/TextFieldMask';
import Tooltip, { CustomizeTooltip } from '../../../components/Tooltips';
import Market from '../../market/class'
import Switch from '../../../components/Switch';
import AddCircleOutlineRoundedIcon from '@material-ui/icons/AddCircleOutlineRounded';
import HighlightOffRoundedIcon from '@material-ui/icons/HighlightOffRounded';

const ActionButton = ({ type="add", onClick, title, color="primary" }) => {
    return(
        <Box display="flex" width="100%" justifyContent="end" >
            <Tooltip title={title} placement='left' >
                <IconButton color={color} size='small' onClick={onClick} >
                    {type === "add" ? <AddCircleOutlineRoundedIcon /> : <HighlightOffRoundedIcon />}
                </IconButton>
            </Tooltip>
        </Box>
    )
}

const PriceForm = ({ priceList=[], onProduct, compount, productBasicData }) => {
    const market = new Market()

    React.useEffect(() => {
        
    },[])
    
    return(
        <>
            <Box mb={2} >
                <Paper variant='outlined' elevation={0} >
                    <Box display="flex" flexDirection="column" p={2} maxHeight="500px" overflow="hidden auto" >
                        { priceList.map((price, index) => {
                            return(
                                <React.Fragment key={index}  >
                                    <Box display="inline-flex" mb={2} alignItems="center" >
                                        <CustomizeTooltip 
                                            title={<p> <b>Asignar como precio de venta.</b> <br /> Este sera el precio visible para los clientes en la tienda virtual, en caso de no seleccionar ninguno sera asignado el primer precio de la lista como precio de venta para los clientes </p>} >
                                                <Box>
                                                    <Switch
                                                        name='favoriteSalePrice'
                                                        checked={price.favoriteSalePrice}
                                                        onChange={(checked) => onProduct.selectAsSellingPrice(index, checked) }
                                                    />
                                                </Box>
                                        </CustomizeTooltip>
                                        { index <= 0 ? <ActionButton title="Añadir nuevo precio" onClick={ () => onProduct.addPrice() } type="add" /> : <ActionButton title="Remover precio" type="remove" onClick={ () => onProduct.removePrice(index) } color="secondary" /> }
                                    </Box>

                                    <Box>
                                        <TextField
                                            required
                                            label="Identificador"
                                            variant="outlined"
                                            value={price.identifier}
                                            name="identifier"
                                            onChange={({attr, value}) => onProduct.updatePriceListValues(index, attr, value)}
                                        />
                                        {index >= 1 ? null : 
                                            <TextField
                                                {...( Boolean(productBasicData.unitSize._id) && {helperText : 
                                                    <b>
                                                        {`${Boolean(productBasicData.unitSize.compoundUnitOfMeasure) ? `Costo por ${productBasicData.unitSize.baseUnit.name} : ` : `Costo por ${productBasicData.unitSize.name} :`} $ ${market.currencyFormat(productBasicData.costMin)}`}
                                                    </b>}
                                                )}
                                                required
                                                label="Costo del producto"
                                                variant="outlined"
                                                value={price.cost}
                                                name="cost"
                                                // disabled={index >= 1 ? true : false}
                                                disabled={compount}
                                                onChange={({attr, value}) => onProduct.updatePriceListValues(index, attr, value)}
                                                InputProps={{
                                                    inputComponent : TextFieldMask
                                                }}
                                                inputProps={{
                                                    style : { fontWeight : "600" },
                                                    value : price.cost
                                                }}
                                            />
                                        }
                                        <TextField
                                            label="Precio de venta"
                                            variant="outlined"
                                            value={price.salePrice}
                                            name="salePrice"
                                            onChange={({attr, value}) => onProduct.updatePriceListValues(index, attr, value)}
                                            InputProps={{
                                                inputComponent : TextFieldMask
                                            }}
                                            inputProps={{
                                                style : { fontWeight : "600" },
                                                value : price.salePrice
                                            }}
                                        />
                                        <TextField
                                            label="Porcentaje de ganancia"
                                            variant="outlined"
                                            value={price.percentageProfit}
                                            name="percentageProfit"
                                            onChange={({attr, value}) => onProduct.updatePriceListValues(index, attr, value)}
                                            InputProps={{
                                                inputComponent : TextFieldMask,
                                            }}
                                            inputProps={{
                                                prefix : "%",
                                                value : price.percentageProfit,
                                            }}
                                        />
                                        <Typography component="span" >
                                            <Box>
                                                Ganancia : <Box component="span" fontWeight="bold" >$ { market.currencyFormat(price.profit) } </Box>
                                            </Box>
                                        </Typography>
                                    </Box>
                                    <Box my={2} >
                                        <Divider />
                                    </Box>
                                </React.Fragment>
                            )
                        }) }
                    </Box>
                </Paper>
            </Box>
        </>
    )
}

export default PriceForm