import { Box, Divider, Grid, Typography } from '@material-ui/core'
import React from 'react'
import Carouse from '../../../components/Carousel'
import Dialog from '../../../components/Dialog'
import Text from '../../../components/Text'
import { currencyFormat } from '../../../methods/parseData'
import Market from '../../market/class'
import UnitSize from '../../unitSize/_class'
import _Product from '../class'
import Product from '../_class'

export const ShowData = ({title, text='', children}) => {
    return(
        <>
            <Box p={1} mb={1} >
                <Text text={title} lineHeight={0} mb={0} uppercase fontSize="small" />
                <Typography >
                    <Box component="span" >{text === "" ? "No hay datos para mostrar" : text}</Box>
                </Typography>
                {children}
            <Divider />
            </Box>
        </>
    )
}

const ViewProduct = ({ isOpenModal, productSelected={}, isCompount, ...actions }) => {
    const { changeProductInformationWindowStatus, selectProductInformation } = actions
    const data = new Product({data: productSelected, viewData: isOpenModal}).reviewData()
    const market = new Market()
    const product = new _Product()
    const unitSizes = product.unitSize(productSelected.unitSize)
    React.useEffect(() => {
        if( !isOpenModal ){
            selectProductInformation({})
        }
    },[isOpenModal])
    return(
        <>
            <Dialog open={isOpenModal} close={() => changeProductInformationWindowStatus(false)} title='Informacion del producto' maxWidth='xs' >
                {Object.keys(data).length >= 11&&
                    <>
                        {Boolean(data.images) &&
                            <Carouse images={data.images} />
                        }

                        <ShowData title="estado:" text={data.status} />
                        <ShowData title="nombre:" text={data.name} />
                        <ShowData title="Codigo SKU:" text={data.skucode} />
                        <ShowData title="Codigo de barras:" text={data.barcode} />
                        <ShowData title="Codigo del proveedor:" text={data.providercode} />
                        <ShowData title="Categoria:" text={ data.category } />
                        <ShowData title="Unidad de medida:" text={data.fullUnitSize} />
                        <ShowData title="Se maneja en inventario:" text={data.useStock} />
                        <ShowData title="Cantidad disponible:" text={data.stock} />
                        <ShowData title="Notificar cuando existan solo:" text={data.stockAlert} />
                        <ShowData title="Compuesto:" text={data.isCompount} />
                        <ShowData title={`Costo por ${data.baseUnitSize}`} text={data.costMin} />
                        <ShowData title="Precios:" text=' ' >
                            {productSelected.prices.map((price, index) => {
                                return(
                                    <Box key={index} >
                                        <ShowData title={price.identifier}>
                                            <Grid container >
                                                <Grid item xs={6} > COSTO.: ${ currencyFormat(productSelected.prices[0].cost) } </Grid>
                                                <Grid item xs={6} >P.V.: ${ currencyFormat(price.salePrice) }</Grid>
                                                <Grid item xs={6} >GANANCIA.: ${ currencyFormat(price.profit) }</Grid>
                                                <Grid item xs={6} >P.G.: { price.percentageProfit }%</Grid>
                                            </Grid>
                                        </ShowData>
                                    </Box>
                                )
                            })}
                        </ShowData>
                        {productSelected.isCompount &&
                            <ShowData title="Compuesto por:" text=" " >
                                {productSelected.compount.map((item, index) => {
                                    return(
                                        <Box key={index} >
                                            <ShowData title={item.product.name} text=" " >
                                                <Grid container >
                                                    <Grid item xs={12} >COSTO.: $ { currencyFormat(item.totalCost??0) } </Grid>
                                                    <Grid item xs={12} > CANTIDAD.: {item.quantity} {UnitSize.getUnitSize(item.product.unitSize, item.positionUnitSize).name} </Grid>
                                                    {item.main &&
                                                        <Grid item xs={12} > PRINCIPAL.: ES PRINCIPAL </Grid>
                                                    }
                                                </Grid>
                                            </ShowData>
                                        </Box>
                                    )
                                })}
                            </ShowData>
                        }
                        <ShowData title="Descripcion:" text={data.description} />
                        <ShowData title="Registrado:" text={data.createdAt} />
                    </>
                }
                
            </Dialog>
        </>
    )
}

export default ViewProduct