import React from 'react'
import { Box, Button as Btn, CircularProgress, IconButton as ButtonIcon, makeStyles } from '@material-ui/core'
import CloseIcon from '@material-ui/icons/Close';
import clsx from 'clsx';

//size : small, medium, large

const useStyles = makeStyles((theme) => ({
    primary:{
        backgroundColor: theme.palette.primary.light,
        color: theme.palette.primary.main,
        padding: "5px",
        borderRadius: 8,
        "&:hover": {
            backgroundColor: theme.palette.primary.main,
            color: theme.palette.primary.light,
        }
    },
    secondary:{
        backgroundColor: theme.palette.secondary.light,
        color: theme.palette.secondary.main,
        padding: "5px",
        borderRadius: 8,
        "&:hover": {
            backgroundColor: theme.palette.secondary.main,
            color: theme.palette.secondary.light,
        }
    },
    buttonAddorn: {
        color: theme.palette.primary.main,
        backgroundColor: "#FFF",
        borderRadius: theme.shape.borderRadius,
        boxShadow: "none",
        marginBottom: "5px",
        "&:hover" : {
            boxShadow: "none",
            color: theme.palette.primary.main,
            backgroundColor: theme.palette.primary.light,
            borderRadius: theme.shape.borderRadius,
        },
        "&:active": {
            boxShadow: "none",
            color: theme.palette.primary.main,
            borderRadius: theme.shape.borderRadius,
        },
    },
}))

export const ClearButton = ({ size="small", color="primary", onClick=false }) => {
    return(
        <ButtonIcon color={color} size={size} onClick={ _ => typeof onClick === "function" && onClick()} >
            <CloseIcon />
        </ButtonIcon>
    )
}
export const LoadingButton = ({ variant="contained", color="primary", size='medium', fullWidth=true, disabled=false }) => {
    return(
        <Btn
            disabled={true}
            fullWidth={fullWidth}
            variant={variant}
            color={color}
            type="submit"
            size={size}
        >
            <CircularProgress size="small" style={{ width : "25px", height : "25px", marginLeft : "25px" }} />
        </Btn>
    )
}
const Button = ({ children, special, variant="contained", color="primary", size='medium', fullWidth=true, startIcon, disabled=false, style=false, onClick=false, type="submit" }) => {
    const clasess = useStyles()
    const clsxStyle = clsx({[clasess.buttonAddorn]: special === "primary"})
    return(
        <Btn
            onClick={() => {
                if( typeof onClick === "function" ){
                    onClick()
                    return
                }else{
                    return
                }
            }}
            disabled={disabled}
            {...(startIcon ? { startIcon } : {})}
            {...(style ? { style } : {})}
            fullWidth={fullWidth}
            variant={variant}
            color={color}
            type={type}
            size={size}
            className={clsxStyle}
        >
            { children }
        </Btn>
    )
}
export const IconButton = ({children, variant="primary", justify="left", align="center", onClick }) => {
    const clasess = useStyles()
    const clsxStyles = clsx({
        [clasess.primary] : variant === "primary",
        [clasess.secondary] : variant === "secondary",
    })
    return(
        <>
            <Box display="flex" justifyContent={justify} alignItems={align} >
                <ButtonIcon className={clsxStyles} onClick={() => typeof onClick === "function" ? onClick() : null} >
                    {children}
                </ButtonIcon>
            </Box>

        </>
    )
}

export default Button