export function objectKeysData(object){
    return Object.keys(object).length >= 1 ? true : false
}
export default function parseData(data){
    if( Boolean(data) ){
        return JSON.parse(JSON.stringify(data))

    }else{
        return data
    }
}
export function createFormData(data, images){
    let formData = new FormData()
    formData.append('data', JSON.stringify(data))
    if( Array.isArray(images) && images.length >= 1 ){
        for(let image of images){
            formData.append('images', image)
        }
    }
    return formData
}
export function deleteElement(array=[], callback=null, staticIndex){
    const data= parseData(array)
    const index = Boolean(callback) ? data.findIndex(callback) : staticIndex
    if( typeof index === "number" ){
        data.splice(index, 1)
    }
    return data
}
export function getObjectValueByName(obejct, objectName){
    if( typeof obejct === 'object' && Object.keys(obejct).length >= 1 ){
        if( Boolean(obejct[objectName]) ){
            return obejct[objectName]
        }else{
            return undefined
        }
    }else{
        return undefined
    }
}
export function currencyFormat(value){
    const currency = Intl.NumberFormat('es-CO')
    const result = isNaN(value) ? 0 : currency.format(value)
    return result
}
export function addParamsToURL(url, filter={}){
    let path = url
    for(let key of Object.keys(filter) ){
        if( filter[key] === undefined ||  filter[key] === null || String(filter[key]).trim() === "" ){
            delete filter[key]
        }else{
            path = path+`${key}=${filter[key]}&`
        }
    }
    return path
}
export function encodeObject(item={}){
    let x = JSON.stringify(item)
    return Buffer.from(x).toString('base64')
}
export function decodeObject(encode){
    let x = Buffer.from(encode, 'base64').toString('ascii')
    return JSON.parse(x)
}
export async function serverSideImage(images=[], domain=""){
    try {
        let fileData = []
        for( let item of images ){
            let blob = await (await fetch(`${domain}${item}`)).blob()
            if( blob.type !== "text/html" ){
                blob.name = item.split('\\')[item.split('\\').length-1];
                blob.lastModified = new Date();
                const myFile = new File([blob], blob.name, {
                    type: blob.type,
                });
                fileData.push(myFile)
            }
        }
        return fileData
    } catch (error) {
        console.log(error)
    }
    
}