import { Box, Grid, IconButton, InputAdornment } from '@material-ui/core'
import React from 'react'
import Button from '../../../components/Button'
import Dialog from '../../../components/Dialog'
import { DeleteSVG, UnitSize } from '../../../components/icons'
import Text from '../../../components/Text'
import TextField from '../../../components/TextField'
import TextFieldMask from '../../../components/TextFieldMask'
import Tooltip from '../../../components/Tooltips'
import Switch from '../../../components/Switch'

const ConfigCompountProduct = ({ productSelected, isOpenModal, positionUnitSize, quantity, compount, isEditable, mainProduct, onCompount, ...actions }) => {
    const { openCompositeProductConfigurationWindow, updatePositionOfThePuttingUnit, updateSelectedQuantity, resetCompositeProductStatus, } = actions
    const data = onCompount.dataCompount()
    React.useEffect(() => {
        if( !isOpenModal ){
            resetCompositeProductStatus()
        }
    },[isOpenModal])
    return(
        <>
            {Object.keys(data).length > 1 &&
                <Dialog close={() => openCompositeProductConfigurationWindow(false)} open={isOpenModal} title={data.name} >
                    <Grid container >
                        <Grid item xs={12} >
                            <Box display="flex" alignItems="center" justifyContent="space-between" >
                                <Tooltip title='Producto base' placement='right' >
                                    <Box display="inline-flex" mb={2} >
                                        <Switch checked={mainProduct} onChange={ (val) => onCompount.activeMainProduct(val) } />
                                    </Box>
                                </Tooltip>
                                {isEditable &&
                                    <Tooltip title='Eliminar de la lista' >
                                        <IconButton color="primary" onClick={() => onCompount.removeCompountToList()} >
                                            <DeleteSVG />
                                        </IconButton>
                                    </Tooltip>
                                }
                            </Box>

                            <TextField
                                name="unitSizeValue"
                                label="Cantidad"
                                value={quantity}
                                onChange={(e) => {
                                    if( e.value.trim() === "" ) e.value = 1
                                    e.value = parseInt(e.value)
                                    updateSelectedQuantity(e.value)
                                }}
                                InputProps={{
                                    inputComponent : TextFieldMask,
                                    endAdornment: <InputAdornment position="end">
                                        <Tooltip title='Cambiar unidad de medida' >
                                            <IconButton size='small' onClick={() => {
                                                if( productSelected.unitSize.compoundUnitOfMeasure ){
                                                    const position = Boolean(positionUnitSize) ? 0 : 1
                                                    updatePositionOfThePuttingUnit(position)
                                                }
                                            }} >
                                                <UnitSize />
                                            </IconButton>
                                        </Tooltip>
                                    </InputAdornment>,
                                }}
                                inputProps={{
                                    style : { fontWeight : "600" },
                                    value : quantity,
                                    prefix : `(${data.unitSizeInfoText(0)}) x`,
                                    maxVal: data.stock(false)

                                }}
                            />
                            <Text text={`Costo por ${data.unitSizeInfoText(0)}: $ ${data.cost(true)}`} lineHeight={0} mb={0} /> <br />
                            <Text text={`Total: $ ${data.total(true)}`} lineHeight={0} mb={0} /> <br />
                            {data.useStock &&
                                <Text text={`Disponibles: ${data.stock()} ${data.unitSizeInfoText(0)}`} lineHeight={0} mb={0} />
                            }
                        </Grid>
                        <Grid item xs={12} >
                            <Box my={2} >
                                <Button size="large" variant='outlined' color='secondary' onClick={() => onCompount.addCompountToList()} > { isEditable ? "Guardar cambios" : "Añadir producto" }</Button>
                            </Box>
                        </Grid>
                    </Grid>
                </Dialog>
            }
        </>
    )
}

export default ConfigCompountProduct