import app from "../../connection/Api"
import history from "../../handlers/history"
import parseData, { addParamsToURL } from '../../methods/parseData'

const methodsDefault = {
    alert: () => null,
    callCustomers: () => null,
}

export default class Customer{
    constructor({ page=1, list=[], limit=10, filter={}, data={}, methods=methodsDefault }){
        this.list = list
        this.data = data
        this.page = page
        this.limit = limit
        this.filter = filter
        this.methods = methods
    }
    updateFormAtributes = ({attr, value}) => {
        const form = parseData(this.data)
        form[attr] = value
        this.methods.updateForm(form)
    }
    async submitFormCustomer(type='create', model={}){
        switch(type){
            case "create":
                let customerCreate = await this.create(this.data)
                if( customerCreate.status ){
                    this.methods.activeNotification(true, "Cliente creado con exito", "success")
                    this.methods.handlerCustomerForm(false)
                    return true
                }else{
                    return false
                }
            case "update":
                let customerUpdate = await this.update({...model, ...this.data})
                if( customerUpdate.status ){
                    this.methods.activeNotification(true, "Los datos del cliente an sido actualizados con exito", "success")
                    this.methods.handlerCustomerForm(false)
                    
                    return true
                }else{
                    return false
                }
            default:
            return
        }
        
    }
    async update(data){
        const customer = await app.post('/update/customer', {customer: data})
        if(customer.error){
            this.methods.activeNotification(true, customer.message, "error")
            return {}
        }
        return customer
    }
    async create(data){
        const customer = await app.post('/save/customer', {customer: data})
        if(customer.error){
            this.methods.activeNotification(true, customer.message, "error")
            return {}
        }
        return customer
    }
    async get(filter={}){
        try {
            let url = addParamsToURL(`/find/customer?page=${this.page}&limit=${this.limit}&`, filter)
            let customers = await app.get(url)
            if(customers.error){
                this.methods.activeNotification(true, customers.message, "error")
                return {}
            }
            return customers
        } catch (error) {
            console.log(error)
        }
    }
    async loadAutocompleteList(text=""){
        try {
            let list = [...this.list]
            const result = await this.get({text})
            if( result.status ){
                let newList = [...list, ...result.payload]
                let hash = {}
                newList = newList.filter(o => hash[o._id] ? false : hash[o._id] = true)
                this.methods.loadCustomersToTheList(newList)
            }
        } catch (error) {
            console.log(error)
        }
    }
    async deletes(ids){
        try{
            const result = await app.post(`/delete/customers`, { customers: ids })
            const flexion = result.payload.success >= 2 ? ["s", "ron"] : ["", ""]
            const message = `La${flexion[0]} categoria${flexion[0]} fue${flexion[1]} eliminada${flexion[0]} con exito`
            if( result.payload.success ){
                this.methods.alert(true, message, "success")
                this.methods.callCustomers()
            }
        }catch(err){
            console.log(err)
        }
    }
}

export const goManageBusiness = (businessName) => history.push(`/${businessName}/manage/customers`)