import * as React from 'react';
import { DataGrid } from '@material-ui/data-grid';
import { ProgressSectionApp } from '../components/ProgressApp'
import { makeStyles } from '@material-ui/core';
import { connect } from 'react-redux';
import { updatePage, updatePagination } from '../redux/actions/DashboardAction';

const locale = {
  noRowsLabel : 'Tabla vacia',
  noResultsOverlayLabel: 'No se han encontrado resultados.',
  errorOverlayDefaultLabel: 'Ocurrió un error.',
  toolbarDensity: 'Densidad',
  toolbarDensityLabel: 'Densidad',
  toolbarDensityCompact: 'Compacto',
  toolbarDensityStandard: 'Estándar',
  toolbarDensityComfortable: 'Cómodo',
  toolbarColumns: 'Columnas',
  toolbarColumnsLabel: 'Seleccionar columnas',
  toolbarFilters: 'Filtros',
  toolbarFiltersLabel: 'Mostrar filtros',
  toolbarFiltersTooltipHide: 'Ocultar filtros',
  toolbarFiltersTooltipShow: 'Mostrar filtros',
  toolbarFiltersTooltipActive: (count) => count !== 1 ? `${count} filtros activos` : `${count} filtro activo`,
  toolbarExport: 'Exportar',
  toolbarExportLabel: 'Exportar',
  toolbarExportCSV: 'Descargar como CSV',
  toolbarExportPrint: 'Impresión',
  columnsPanelTextFieldLabel: 'Buscar columna',
  columnsPanelTextFieldPlaceholder: 'Título de la columna',
  columnsPanelDragIconLabel: 'Reordenar columna',
  columnsPanelShowAllButton: 'Mostrar todo',
  columnsPanelHideAllButton: 'Ocultar todo',
  filterPanelAddFilter: 'Añadir filtro',
  filterPanelDeleteIconLabel: 'Borrar',
  filterPanelOperators: 'Operadores',
  filterPanelOperatorAnd: 'Y',
  filterPanelOperatorOr: 'O',
  filterPanelColumns: 'Columnas',
  filterPanelInputLabel: 'Valor',
  filterPanelInputPlaceholder: 'Valor del filtro',
  filterOperatorContains: 'Contiene',
  filterOperatorEquals: 'es igual',
  filterOperatorStartsWith: 'comienza con',
  filterOperatorEndsWith: 'termina con',
  filterOperatorIs: 'es',
  filterOperatorNot: 'no es',
  filterOperatorAfter: 'es despues',
  filterOperatorOnOrAfter: 'está en o después',
  filterOperatorBefore: 'es antes',
  filterOperatorOnOrBefore: 'está en o antes',
  filterOperatorIsEmpty: 'Está vacío',
  filterOperatorIsNotEmpty: 'No esta vacío',
  filterOperatorIsAnyOf: 'es cualquiera de',
  filterValueAny: 'ningún',
  filterValueTrue: 'Verdadero',
  filterValueFalse: 'Falso',
  columnMenuLabel: 'Menu',
  columnMenuShowColumns: 'Mostrar columnas',
  columnMenuFilter: 'Filtrar',
  columnMenuHideColumn: 'Esconder',
  columnMenuUnsort: 'Desclasificar',
  columnMenuSortAsc: 'Ordenar por ASC',
  columnMenuSortDesc: 'Ordenar por DESC',
  columnHeaderFiltersTooltipActive: (count) => count !== 1 ? `${count} filtros activos` : `${count} filtro activo`,
  columnHeaderFiltersLabel: 'Mostrar filtros',
  columnHeaderSortIconLabel: 'Clasificar',
  footerRowSelected: (count) =>
    count !== 1
      ? `${count.toLocaleString()} Filas seleccionadas`
      : `${count.toLocaleString()} Fila seleccionada`,
  footerTotalRows: 'Filas totales:',
  footerTotalVisibleRows: (visibleCount, totalCount) =>
    `${visibleCount.toLocaleString()} de ${totalCount.toLocaleString()}`,
  checkboxSelectionHeaderName: 'Selección de casilla de verificación',
  booleanCellTrueLabel: 'Verdadero',
  booleanCellFalseLabel: 'Falso',
  actionsCellMore: 'más',
  pinToLeft: 'Pin a la izquierda',
  pinToRight: 'Pin a la izquierda',
  unpin: 'Desprender',
  treeDataGroupingHeaderName: 'Grupo',
  treeDataExpand: 'Ver niños',
  treeDataCollapse: 'Ocultar niños',
  groupingColumnHeaderName: 'Grupo',
  groupColumn: (name) => `Agrupar por ${name}`,
  unGroupColumn: (name) => `Deja de agrupar por${name}`,
}
const useStyles = makeStyles({
  root : {
    '&.MuiDataGrid-root .MuiDataGrid-cell:focus': {
      outline: 'none',
    },
  }
})

const mapPropsToState = (state) => {
  return{
    page : state.Dashboard.pagination.page,
    dataPerPage : state.Dashboard.pagination.dataPerPage,
    totalList : state.Dashboard.pagination.totalList,
  }
}

const mapDispatchToProps = (dispatch) => {
  return{
    updatePagination: (totalPages, totalList, dataPerPage) => dispatch(updatePagination(totalPages, totalList, dataPerPage)),
    updatePage: (page) => dispatch(updatePage(page)),
  }
}

const Table = connect(mapPropsToState, mapDispatchToProps)(({ rows=[], columns=[], rowCount, updatePage, loading, page, overlayText="Cargando categorias", getRowId=row => row._id, checkboxSelection=false, onSelection, selectionModel, hideFooter, pointer, onCellClick, disableFocus=true, dataPerPage, totalList, updatePagination }) => {
  const Overlay = () => {
    return <ProgressSectionApp label={overlayText} />
  }
  const clasess = useStyles()
  
  React.useEffect(() => {
    return () => {
      updatePagination(1 ,1 ,10)
      updatePage(1)
    }
  },[])

  return (
    <div style={{ height: "auto", width: '100%', backgroundColor: "#FFFFFF", borderRadius: "10px" }}>
      <DataGrid
        {...(disableFocus && { className : clasess.root } )}
        {...(pointer && { style : { cursor: "pointer" } })}
        onRowClick={(result) => typeof onCellClick === "function" ? onCellClick(result) : null }
        hideFooter={hideFooter}
        autoHeight
        checkboxSelection={checkboxSelection}
        {...( typeof onSelection === "function" ? { onSelectionModelChange : (items) => {
          onSelection(items)
        } } : {} )}
        {...(selectionModel ? { selectionModel } : {})}
        localeText={locale}
        disableColumnMenu
        getRowId={getRowId}
        rows={rows}
        columns={columns}
        page={(page-1) || 0}
        pageSize={dataPerPage}
        disableSelectionOnClick
        rowCount={totalList}
        paginationMode="server"
        onPageChange={(newPage) => updatePage(newPage+1)}
        // onPageChange={(newPage) => typeof updatePage === "function" ? updatePage(newPage+1) : null }
        loading={!loading}
        components={{
          LoadingOverlay: Overlay,
        }}
      />
    </div>
  );
})

export default Table