import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import { Avatar, Box, Drawer } from '@material-ui/core';
import Dashboard from '../class';
import { ListCollapse } from '../../../components/ListItem';
import Responsive from '../../../components/Responsive';
import { usePhotoProfile } from '../../../hooks/usePhotoProfile';
import avatar from '../../../styles/svg/018-account.svg'

const Sidebar = ({ children, business, setOpenDrawer, openDrawe }) => {
  const [drawerWidth, setDrawerWidth ] = React.useState(300)
  const [typeDrawer, setTypeDrawer ] = React.useState("persistent")
  const useStyles = makeStyles((theme) => ({
    root: {
      display: 'flex',
      height: "auto"
    },
    drawer: {
      width: drawerWidth,
      flexShrink: 0,
      borderWidth: 0
    },
    drawerMovile: {
      width: '100%',
      flexShrink: 0,
    },
    drawerPaper: {
      width: 300,
      padding: 10
    },
    drawerHeader: {
      display: 'flex',
      alignItems: 'center',
      padding: theme.spacing(0, 1),
      ...theme.mixins.toolbar,
      justifyContent: 'flex-end',
    },
    content: {
      height: "100%",
      marginTop: "40px",
      borderRadius: "12px",
      flexGrow: 1,
      paddingLeft: theme.spacing(3),
      paddingRight: theme.spacing(3),
      transition: theme.transitions.create('margin', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
      marginLeft: -drawerWidth,
    },
    contentShift: {
      transition: theme.transitions.create('margin', {
        easing: theme.transitions.easing.easeOut,
        duration: theme.transitions.duration.enteringScreen,
      }),
      marginLeft: 0,
    },
    drawerContainer: {
      overflow: 'auto',
    },
  }));
  const classes = useStyles();
  return (
    <Responsive
      mediaFunctions={{
        xs: () => {
          setDrawerWidth(0)
          setTypeDrawer("temporary")
        },
        sm: () => {
          setDrawerWidth(300)
          setTypeDrawer("persistent")
        },
        md: () => {
          setDrawerWidth(300)
          setTypeDrawer("persistent")
        },
        lg: () => {
          setDrawerWidth(300)
          setTypeDrawer("persistent")
        },
      }}
    >
      <div className={classes.root}>
        <Drawer className={classes.drawer} variant={typeDrawer} anchor="left" open={openDrawe}
          PaperProps={{style: {border: 'none'}}}
          onClose={() => setOpenDrawer(false)}
          classes={{
            paper: classes.drawerPaper,
          }}
        >
					{/* <Avatar alt="business-photo" src={Boolean(photo) ? photo : avatar } style={{ height : "30px", width : "30px" }} /> */}
          <ListCollapse list={Dashboard.sidebar(business, usePhotoProfile)} />
        </Drawer>
        <Box component="span" width="100%" p={2} >
          <main className={ clsx(classes.content, openDrawe && { [classes.contentShift]: openDrawe && Boolean(drawerWidth),})} >
              { children }
          </main>
        </Box>
      </div>
    </Responsive>
  );
}

export default Sidebar