import constant from './index'
import { updatePagination } from './DashboardAction'
import store from '../store'
import Categorie from '../../views/categories/class'

export const getCategories = ({match="", staticPage=0}) => async (dispatch) => {
	try{
		const page = staticPage === 0 ? store.getState().Dashboard.pagination.page : staticPage
		const category = new Categorie({ page })
		const categories = await category.get({ match })
		if( categories.status ){
			dispatch(updatePagination(
				categories.data.pages_total,
				categories.data.categories_total,
				categories.data.data_per_page
				))
		}
		dispatch(saveListOfCategories(categories.payload))
	}catch(err){
		console.log(err)
	}
}
export const saveListOfCategories = (categories) => {
	return {type: constant.SAVE_LIST_OF_CATEGORIES, categories }
}
export const updateTheTypeOfCategoryForm = (typeForm) => {
	return {type: constant.UPDATE_THE_TYPE_OF_CATEGORY_FORM, typeForm }
}
export const isTheSaleOpenWithTheCategoryForm = (value) => {
	return {type: constant.IS_THE_SALE_OPEN_WITH_THE_CATEGORY_FORM, value }
}
export const updateTheCategoryForm = (form) => {
	return {type: constant.UPDATE_THE_CATEGORY_FORM, form }
}
export const selectThisCategory = (category_selected) => {
	return {type: constant.SELECT_THIS_CATEGORY, category_selected }
}
export const selectCategoriesToDelete = (categories) => {
	return {type: constant.SELECT_CATEGORIES_TO_DELETE, categories }
}
export const deleteMultipleCategories = (value) => {
	return {type: constant.DELETE_MULTIPLE_CATEGORIES, value }
}