import React from 'react'
import TransferList from '../../components/TransferList'
import { Box } from '@material-ui/core'
import Chip from '../../components/Chip'
import { getPermissions, getRoles, openFormCreateRol, selectRol, updateFormCreateRol, updateFormType } from '../../redux/actions/RolesAndPermissionsAction'
import { connect } from 'react-redux'
import Permission from './class'
import { callNotificationSnackbar, isOpenSecureDialog } from '../../redux/actions/DashboardAction'
import EditIcon from '@material-ui/icons/Edit';
import TitleViews from '../../components/TitleViews'
import { RoleSVG } from '../../components/icons'
import CreateRole from './create_role'
import SecureDialog from '../../components/SecureDialog'

const mapDispatchToProps = (dispatch) => {
    return{
        getPermissions: () => dispatch(getPermissions()),
        getRoles: () => dispatch(getRoles()),
        openFormCreateRol: (value) => dispatch(openFormCreateRol(value)),
        updateFormType: (type) => dispatch(updateFormType(type)),
        updateFormCreateRol: (form) => dispatch(updateFormCreateRol(form)),
        selectRol: (role) => dispatch(selectRol(role)),
        isOpenSecureDialog: (value) => dispatch(isOpenSecureDialog(value)),
        callNotificationSnackbar: (active, message, severity) => dispatch(callNotificationSnackbar(active, message, severity)),
    }
}

const mapPropsToState = (state) => {
    return{
        permissions: state.Permissions.permissions,
        roles: state.Permissions.roles,
        isOpenModal: state.Permissions.create_role.isOpenModal,
        type: state.Permissions.create_role.type,
        form: state.Permissions.create_role.form,
        roleSelectedToUpdate: state.Permissions.update_role.roleSelected,
    }
}

const Permissions = connect(mapPropsToState, mapDispatchToProps)(({permissions, roles, isOpenModal, type, form, roleSelectedToUpdate, ...actions}) => {
    const [roleSelected, setRoleSelected] = React.useState(0)
    const [permissionsAvailable, setPermissionsAvailable] = React.useState([])
    const [permissionsEnabled, setPermissionsEnabled] = React.useState([])
    const { getPermissions, isOpenSecureDialog, getRoles, callNotificationSnackbar, openFormCreateRol, updateFormType, updateFormCreateRol, selectRol } = actions
    const permit = new Permission({
        form,
        roleForm: type,
        role: type === "create" ? roles[roleSelected] : roleSelectedToUpdate,
        methods:{
            alert: callNotificationSnackbar,
            callRoles: getRoles,
            selectRol,
            openFormCreateRol,
        }
    })

    const organizeRoles = React.useCallback(() => {
        let permitsRest = []
        if( permissions.length >= 1 && roles.length >= 1 ){
            let permitsSelected = roles[roleSelected].permissions
            for(let item of permissions){
                const x = permitsSelected.some(n => n._id === item._id)
                if( !x ){
                    permitsRest.push(item)
                }
            }
            setPermissionsAvailable(permitsRest)
            setPermissionsEnabled(permitsSelected)
        }
    },[roleSelected, roles])
    React.useEffect(() => {
        getPermissions()
        getRoles()
    },[])
    React.useEffect(() => {
        organizeRoles()
    },[organizeRoles])
    React.useEffect(() => {
        setRoleSelected(0)
    },[roles])
    return(
        <>
            <TitleViews
                mb={2}
                title="Roles y permisos"
                tooltip="Crear nuevo rol"
                onClick={() => openFormCreateRol(true) }
                icon={<RoleSVG />}
            />
            <Box display="flex" flexDirection="column" alignItems="center" >
                <Box display="flex" mb={2} >
                    {roles.length >= 1 &&
                        roles.map((item, index) => {
                            return(
                                <React.Fragment key={index} >
                                    <Box mx={1}>
                                        <Chip
                                            size='medium'
                                            label={item.name}
                                            onClick={() => setRoleSelected(index)}
                                            color="primary"
                                            variant={roleSelected === index ? "default" : "outlined"}
                                            deleteIcon={<EditIcon />}
                                            onDelete={() => {
                                                selectRol(roles[index])
                                                updateFormType("update")
                                                openFormCreateRol(true)
                                            }}
                                        />
                                    </Box>
                                </React.Fragment>
                            )
                        })
                    }
                </Box>
                <TransferList
                    onChange={ (vals) => permit.updatePermissionsInRole(vals) }
                    list={permissionsAvailable}
                    selectedDefault={permissionsEnabled}
                    titleLeft='Permisos disponibles'
                    titleRight='Permisos seleccionados'
                />
            </Box>

            <CreateRole
                isOpenModal={isOpenModal}
                type={type}
                form={form}
                selectRol={selectRol}
                openFormCreateRol={openFormCreateRol}
                updateFormType={updateFormType}
                updateFormCreateRol={updateFormCreateRol}
                permissionsClass={permit}
                roleSelected={roleSelectedToUpdate}
                isOpenSecureDialog={isOpenSecureDialog}
            />

            <SecureDialog
                textButton="Eliminar rol"
                onSubmit={() => permit.wipeOutRole() }
            />
        </>
    )
})

export default Permissions